import once from 'lodash/once';
import Promise from 'promiscuous';
import {always} from './utils';


export function ChallengeManagerFactory() {
    const challengeManager = {};

    /*
     * Set the challenge handler which is a function that will receive challenges.
     * @param {Function} handler A function which will receive authentication challenges.
     * @param {Function} handler.retry A function which once called, will give resume the execution queue where it left
     *                                 off before the challenge using the answer to the challenge.
     * @param {any}      handler.retry.answer The challenge answer to pass to the retry function that will resolve the challenge.
     * @param {Function} handler.cancel A function which once called with resume the execution where it left of but will
     *                                  let it down the error path since the challenge was not answered.
     */
    challengeManager.setChallengeHandler = function(handler) {
        if (typeof handler !== 'function') {
            throw new TypeError('Challenge handler is not a function.');
        }
        challengeManager.challengeHandler = handler;
    };

    /*
     * Sets an answer handler to give a chance for the anybody using challenges to get notified whenever
     * a challenge gets answered. This allows to store the challenge answer by the app.
     *
     * @param {Function} handler Answer handler which will be called whenever a challenge is answered.
     * @param {any}      handler.answer The answer to the challenge.
     */
    challengeManager.setAnswerHandler = function(handler) {
        if (typeof handler !== 'function') {
            throw new TypeError('Challenge handler is not a function.');
        }
        challengeManager.challengeAnswerHandler = handler;
    };

    /*
     * Starts a challenge to the application. A challenge can be anything that the application needs to provide
     * for execution to continue. A challenge creates a promise for an answer. At which point it is up
     * to the challenger to interpret the result of the challenge.
     *
     * @return {Promise} A promise for an answer to the challenge. The promise will reject if the challenge is
     *                   canceled by the application.
     */
    challengeManager.challenge = function() {
        if (challengeManager.challengeSuspensions > 0) {
            return new Promise.reject();
        }

        // If we don't already have a challenge to the application in progress, create one.
        if (!challengeManager.currentChallenge) {
            challengeManager.currentChallenge = always(new Promise((resolve, reject) => {
                if (challengeManager.challengeHandler) {
                    var retry = once(function(params) {

                        if (challengeManager.challengeAnswerHandler) {
                            challengeManager.challengeAnswerHandler(params);
                        }

                        // Resolve the challenge and allow continuation of paused requests.
                        resolve(params);
                    });

                    var cancel = once(function() {
                        // Reject the challenge
                        reject();
                    });

                    challengeManager.challengeHandler(retry, cancel);
                } else {
                    reject();
                }
            }), () => challengeManager.currentChallenge = undefined);
        }

        return challengeManager.currentChallenge;
    };


    /*
     * Temporarily suspend any challenges.
     * @param  {Promise} waitOn Promise on which to wait for being settled before resuming challenges
     * @return {Promise}        Continuation of the original promise which will resolve once the challenges
     *                          have been resumed.
     */
    challengeManager.suspendChallenges = function(waitOn) {
        challengeManager.challengeSuspensions++;
        return always(waitOn, () => challengeManager.challengeSuspensions -= 1);
    };

    challengeManager.challengeSuspensions = 0;

    return challengeManager;
}
