import * as actionTypes from '../actionTypes'
import { callActionHelper } from './calls'

/**
 * Represents a request to check media devices.
 * @method checkDevices
 * @returns {Object} A flux standard action.
 */
export function checkDevices () {
  return {
    type: actionTypes.CHECK_DEVICES
  }
}

/**
 * Represents that media devices have been checked.
 * @method checkDevicesFinish
 * @param  {Object} $0
 * @param  {Object} $0.devices
 * @param  {Error} $0.error
 * @returns {Object} A flux standard action.
 */
export function checkDevicesFinish ({ devices, error }) {
  var action = {
    type: actionTypes.CHECK_DEVICES_FINISH
  }

  if (error) {
    action.error = true
    action.payload = error
  } else {
    action.payload = devices
  }
  return action
}

/**
 * Represents a request to set default devices for calls.
 * @method setDefaultDevices
 * @param  {Object} devices
 * @param  {string} [devices.camera] The ID of the camera to set as default.
 * @param  {string} [devices.microphone] The ID of the microphone to set as default.
 * @param  {string} [devices.speaker] The ID of the speaker to set as default.
 * @return {Object} A flux standard action.
 */
export function setDefaultDevices (devices) {
  return {
    type: actionTypes.SET_DEFAULT_DEVICES,
    payload: {
      ...devices
    }
  }
}

/**
 * Represents that the default devices was attempted to be set.
 * @method setDefaultDevicesFinish
 * @param  {Object} $0
 * @param  {Object} $0.devices The devices that were set as defaults.
 * @param  {Object} $0.error Error object, in the case of an issue.
 * @return {Object} A flux standard action.
 */
export function setDefaultDevicesFinish ({ devices, error }) {
  return {
    type: actionTypes.SET_DEFAULT_DEVICES_FINISH,
    payload: error || devices,
    error: !!error
  }
}

/**
 * Represents a request to change the current speaker in use for a call.
 * @method changeSpeaker
 * @param  {string} speakerId Id of the speaker.
 * @return {Object} A flux standard action.
 */
export function changeSpeaker (speakerId) {
  return {
    type: actionTypes.CHANGE_SPEAKER,
    payload: speakerId
  }
}

/**
 * Represents that the current speaker was attempted to be changed.
 * @method changeSpeakerFinish
 * @param  {Object} error Error object, in the case of an issue.
 * @return {Object} A flux standard action.
 */
export function changeSpeakerFinish ({ speakerId, error }) {
  return {
    type: actionTypes.CHANGE_SPEAKER_FINISH,
    payload: error || speakerId,
    error: !!error
  }
}

/**
 * Represents a request to change the camera/microphone for a call.
 * @method changeInputDevices
 * @param  {string} callId The ID of the call to act upon.
 * @return {Object} A flux standard action.
 */
export function changeInputDevices (callId) {
  return callActionHelper(actionTypes.CHANGE_INPUT_DEVICES, callId)
}

/**
 * Represents that input devices was (attempted) changed for a call.
 * @method changeInputDevicesFinish
 * @param  {Object} $0
 * @param  {string} $0.callId   Id of the call that changed.
 * @param  {Object} $0.error Error object, in the case of an issue.
 * @return {Object} A flux standard action.
 */
export function changeInputDevicesFinish ({ callId, error }) {
  return callActionHelper(actionTypes.CHANGE_INPUT_DEVICES_FINISH, callId, {
    error
  })
}
