import Bottle from 'bottlejs';
import loadModule from './src/loadModule';

// Modules
import coreModule from './src/js/module';
import rccCallModule from './src/js/call/rcc/module';
import wamCallModule from './src/js/call/wam/module';
import callModule from './src/js/call/module';
import webRtcModule from './src/js/webrtc/module';



const modules = [
    // Common modules, needed in all scenarios
    coreModule,

    // Feature modules
    callModule,

    // Modules needed for calls.
    rccCallModule,
    wamCallModule,

    // WebRTC Adapter module.
    webRtcModule
];


export function createFcs() {
    const bottle = new Bottle();

    // Load all modules into bottle.
    modules.forEach(loadModule.bind(bottle));

    const container = bottle.container;
    var fcs = bottle.container.Core;

    // Build the namespaces.
    fcs.logManager = container.LogManager;
    fcs.notification = container.Notification;
    fcs.call = container.Call;

    // Convenience access to these.
    fcs.NotificationCallbacks = container.NotificationCallbacks;
    fcs.GlobalBroadcaster = container.GlobalBroadcaster;

    // Run any initialization code setup by the modules.
    bottle.resolve(container);

    return fcs;
}
