import * as actionTypes from '../actionTypes'

/**
 * Represents that local video should be displayed in the container.
 * @method startLocalVideo
 * @param  {HTMLElement} videoContainer The container to use for local video.
 * @returns {Object} A flux standard action.
 */
export function startLocalVideo (videoContainer) {
  return {
    type: actionTypes.START_LOCAL_VIDEO,
    payload: videoContainer
  }
}

/**
 * Represents that a request for local video has finished.
 * @method startLocalVideoFinish
 * @param  {HTMLElement} videoContainer The container that local video is being displayed in.
 * @param  {BasicError} error A Basic error object, if an error occurred.
 * @returns {Object} A flux standard action.
 */
export function startLocalVideoFinish (videoContainer, error) {
  return {
    type: actionTypes.START_LOCAL_VIDEO_FINISH,
    payload: error || videoContainer,
    error: !!error
  }
}

/**
 * Represents that local video should stop being displayed.
 * @method stopLocalVideo
 * @returns {Object} A flux standard action.
 */
export function stopLocalVideo () {
  return {
    type: actionTypes.STOP_LOCAL_VIDEO
  }
}

/**
 * Represents that an action to stop local video has finished.
 * @method stopLocalVideoFinish
 * @param  {BasicError} error A Basic error object, if an error occurred.
 * @returns {Object} A flux standard action.
 */
export function stopLocalVideoFinish (error) {
  return {
    type: actionTypes.STOP_LOCAL_VIDEO_FINISH,
    payload: error || undefined,
    error: !!error
  }
}
