import * as actionTypes from '../actionTypes'
import { combineActions, handleActions } from 'redux-actions'
import { concat } from 'lodash/fp'

// Top-tier reducers: Handles the whole call state.
const reducers = {}
// Call-tier reducers: Handles only a specific call's state.
const callReducers = {}

/*
 * Top-tier reducers.
 * Receives the entire call substate as state.
 */

/*
 * Reducer to handle MAKE_CALL action.
 * Create a new call object and add it to state.
 */
reducers[actionTypes.MAKE_CALL] = {
  next (state, action) {
    const newCall = {
      ...action.payload.callInfo,
      state: 'INITIATING',
      muted: false,
      silenced: false,
      direction: 'outgoing',
      isScreensharing: action.payload.callInfo.sendScreenShare || false,
      originalRemoteParticipant: {
        displayNumber: action.payload.callInfo.to
      },
      id: action.payload.callId
    }
    return concat(state, newCall)
  }
}

/*
 * Reducer to handle CALL_INCOMING actions.
 * Cannot have an error 'incoming call'. Add the
 *      incoming call to state.
 */
reducers[actionTypes.CALL_INCOMING] = {
  next (state, action) {
    const newCall = {
      state: 'RINGING',
      muted: false,
      silenced: false,
      direction: 'incoming',
      isScreensharing: false,
      id: action.payload.callId,
      wrtcsSessionId: action.payload.callInfo.wrtcsSessionId,
      ...action.payload.callInfo
    }
    return concat(state, newCall)
  }
}

reducers[actionTypes.JOIN_CALL_FINISH] = {
  next (state, action) {
    const newCall = {
      ...action.payload.callInfo,
      direction: 'outgoing',
      id: action.payload.callId
    }
    return concat(state, newCall)
  }
}

/*
 * Call-tier reducers.
 * Receive a single call's state as state.
 */

/*
 * Reducer to handle SET_CUSTOM_PARAMETERS actions.
 */
callReducers[actionTypes.SET_CUSTOM_PARAMETERS] = {
  next (state, action) {
    return {
      ...state,
      customParameters: action.payload.customParameters
    }
  }
}

/*
 * Reducer to handle MAKE_CALL_FINISH actions.
 * In both cases, update the existing call in state with
 *      the new call state.
 */
callReducers[actionTypes.MAKE_CALL_FINISH] = {
  next (state, action) {
    const callInfo = action.payload.callInfo
    return {
      ...state,
      ...callInfo,
      state: 'INITIATED',
      sendingVideo: callInfo.sendInitialVideo || false
    }
  },
  throw (state, action) {
    return {
      ...state,
      ...action.payload.callInfo,
      state: 'ENDED',
      error: action.payload
    }
  }
}

/*
 * Reducer to handle ANSWER_CALL_FINISH actions.
 * Only intended to set call info, not current state.
 */
callReducers[actionTypes.ANSWER_CALL_FINISH] = {
  next (state, action) {
    const callInfo = action.payload.callInfo
    return {
      ...state,
      ...callInfo,
      sendingVideo: callInfo.sendInitialVideo || false
      // State change action sets the call's current state.
    }
  },
  throw (state, action) {
    return {
      ...state,
      ...action.payload.callInfo,
      error: action.payload
      // State change action sets the call's current state.
    }
  }
}

/*
 * Reducer to handle ANSWER_CALL actions.
 * Only intended to set call info, not current state.
 */
callReducers[actionTypes.ANSWER_CALL] = {
  next (state, action) {
    const callInfo = action.payload.callInfo
    return {
      ...state,
      ...callInfo,
      sendingVideo: callInfo.sendInitialVideo || false
      // State change action sets the call's current state.
    }
  },
  throw (state, action) {
    return {
      ...state,
      ...action.payload.callInfo,
      error: action.payload
      // State change action sets the call's current state.
    }
  }
}

/*
 * Reducer to handle CALL_STATE_CHANGE actions.
 * Change the specified call's state.
 */
callReducers[actionTypes.CALL_STATE_CHANGE] = {
  next (state, action) {
    return {
      ...state,
      ...action.payload.callInfo,
      state: action.payload.state,
      transition: action.payload.transition
    }
  }
}

/*
 * Reducer to handle media state change actions.
 * Change the specified call's media state.
 */
callReducers[actionTypes.CALL_MEDIA_STATE_CHANGE] = {
  next (state, action) {
    return {
      ...state,
      mediaState: action.payload.mediaState
    }
  }
}

/*
 * Reducer to handle MUTE_CALL_FINISH actions.
 * Change the specified call's state to muted.
 */
callReducers[actionTypes.MUTE_CALL_FINISH] = {
  next (state) {
    return {
      ...state,
      muted: true
    }
  }
}

/*
 * Reducer to handle UNMUTE_CALL_FINISH actions.
 * Change the specified call's state to not muted.
 */
callReducers[actionTypes.UNMUTE_CALL_FINISH] = {
  next (state) {
    return {
      ...state,
      muted: false
    }
  }
}

/*
 * Reducer to handle SILENCE_CALL_FINISH actions.
 * Change the specified call's state to silenced.
 */
callReducers[actionTypes.SILENCE_CALL_FINISH] = {
  next (state) {
    return {
      ...state,
      silenced: true
    }
  }
}

/*
 * Reducer to handle UNSILENCE_CALL_FINISH actions.
 * Change the specified call's state to not silenced.
 */
callReducers[actionTypes.UNSILENCE_CALL_FINISH] = {
  next (state) {
    return {
      ...state,
      silenced: false
    }
  }
}

/*
 * Handles changing the call's screensharing state.
 */
callReducers[actionTypes.START_SCREENSHARE_FINISH] = {
  next (state) {
    return {
      ...state,
      isScreensharing: true
    }
  }
}

/*
 * Handles changing the call's screensharing state.
 */
callReducers[actionTypes.STOP_SCREENSHARE_FINISH] = {
  next (state) {
    return {
      ...state,
      isScreensharing: false
    }
  }
}

/*
 * Handles changing the call's video state.
 */
callReducers[actionTypes.START_VIDEO_FINISH] = {
  next (state) {
    return {
      ...state,
      sendingVideo: true
    }
  }
}

/*
 * Handles changing the call's video state.
 */
callReducers[actionTypes.STOP_VIDEO_FINISH] = {
  next (state) {
    return {
      ...state,
      sendingVideo: false
    }
  }
}

/*
 * Combine all of the call-tier reducers into a single reducer,
 *      each with a default state of empty object.
 */
const callReducer = handleActions(callReducers, {})

// Actions routed to call-tier reducers.
const specificCallActions = combineActions(
  actionTypes.MAKE_CALL_FINISH,
  actionTypes.ANSWER_CALL,
  actionTypes.ANSWER_CALL_FINISH,
  actionTypes.CALL_STATE_CHANGE,
  actionTypes.CALL_MEDIA_STATE_CHANGE,
  actionTypes.MUTE_CALL_FINISH,
  actionTypes.UNMUTE_CALL_FINISH,
  actionTypes.SILENCE_CALL_FINISH,
  actionTypes.UNSILENCE_CALL_FINISH,
  actionTypes.START_SCREENSHARE_FINISH,
  actionTypes.STOP_SCREENSHARE_FINISH,
  actionTypes.START_VIDEO_FINISH,
  actionTypes.STOP_VIDEO_FINISH,
  actionTypes.SET_CUSTOM_PARAMETERS
)

/*
 * Reducer to handle specific call actions.
 * Routes the actions to the call-tier reducers.
 */
reducers[specificCallActions] = (state, action) => {
  return state.map(function (call) {
    // Only update the call related to the action.
    if (call.id === action.payload.callId) {
      return callReducer(call, action)
    } else {
      return call
    }
  })
}

/*
 * Combine all of top-tier reducers into a single reducer,
 *      each with a default state of an empty array.
 */
const reducer = handleActions(reducers, [])
export default reducer
