import * as actionTypes from '../actionTypes'

/**
 * Represents an API request to create an audio bridge.
 * @method createAudioBridge
 * @param  {string} bridgeId UUID to identify the audio bridge.
 * @returns {Object} A flux standard action.
 */
export function createAudioBridge (bridgeId) {
  return {
    type: actionTypes.CREATE_AUDIO_BRIDGE,
    payload: {
      bridgeId
    }
  }
}

/**
 * Represents the result of creating an audio bridge.
 * @method createAudioBridgeFinish
 * @param  {string} bridgeId UUID to identify the audio bridge.
 * @param  {Boolean} [error=false]
 * @returns {Object} A flux standard action.
 */
export function createAudioBridgeFinish (bridgeId, error = false) {
  return {
    error: !!error,
    type: actionTypes.CREATE_AUDIO_BRIDGE_FINISH,
    payload: error || { bridgeId }
  }
}

