    /**
     * States of the Call.
     *
     * This is a SPiDR and RCC service member.
     * Available for SPiDR since 3.0.0, and for RCC since 3.1.1.
     *
     * @name fcs.call#States
     * @enum {number}
     * @readonly
     * @property {number} [IN_CALL=0] The call has been established. In rcc mode, called and calling party information is also passed to the client in on state change callback.
     * @property {number} [ON_HOLD=1] The call has been put on hold.
     * @property {number} [RINGING=2] The outgoing call is ringing. In rcc mode, called and calling party information is also passed to the client in on state change callback.
     * @property {number} [ENDED=3] The call has been terminated.
     * @property {number} [REJECTED=4] The outgoing call request has been rejected by the other party.
     * @property {number} [OUTGOING=5] The outgoing call request has been sent but no response have been received yet.
     * @property {number} [INCOMING=6] The incoming call has been received but has not been answered yet. In rcc mode, called and calling party information is also passed to the client in on state change callback.
     * @property {number} [ANSWERING=7] The incoming call has been answered but the call as not been establish yet.
     * @property {number} [JOINED=8] The call is joined.
     * @property {number} [RENEGOTIATION=9] The call is re-established.
     * @property {number} [TRANSFERRED=10] The call is transferred to a third party.
     * @property {number} [ON_REMOTE_HOLD=11] The call has been put on hold remotely.
     * @property {number} [CALL_IN_PROGRESS=12] The call is in progress. In rcc mode, called and calling party information is also passed to the client in on state change callback.
     * @property {number} [EARLY_MEDIA=13] Early media process successful.
     * EARLY_MEDIA and RINGING states can be triggered multiple times in different orders.
     * In each of these states, if the respondCallUpdate notification comes
     * and processed successfully, the state is IN_CALL.
     * @property {number} [TRANSFER_FAILURE=14] The call couldn't be transferred to a third party.
     * @property {number} [REPLACING=15] Blind Transfer is in progress for transferee. Call replacing notification has come.
     */

    this.States = {
        IN_CALL: 0,
        ON_HOLD: 1,
        RINGING: 2,
        ENDED: 3,
        REJECTED: 4,
        OUTGOING: 5,
        INCOMING: 6,
        ANSWERING: 7,
        JOINED: 8,
        RENEGOTIATION: 9,
        TRANSFERRED: 10,
        ON_REMOTE_HOLD: 11,
        CALL_IN_PROGRESS: 12,
        EARLY_MEDIA: 13,
        TRANSFER_FAILURE: 14,
        REPLACING: 15
    };

    /**
     * States of the Media Negotiation (ICE).
     *
     * This is a SPiDR service only.
     *
     * @name fcs.call#MediaStates
     * @enum {number}
     * @readonly
     * @property {number} [NEW=0] The ICE agent is gathering addresses or is waiting to be given remote candidates.
     * @property {number} [CHECKING=1] The ICE agent has been given one or more remote candidates and is checking pairs of local and remote candidates against one another to try to find a compatible match, but has not yet found a pair which will allow the peer connection to be made. It's possible that gathering of candidates is also still underway.
     * @property {number} [CONNECTED=2] A usable pairing of local and remote candidates has been found for all components of the connection, and the connection has been established. It's possible that gathering is still underway, and it's also possible that the ICE agent is still checking candidates against one another looking for a better connection to use.
     * @property {number} [COMPLETED=3] The ICE agent has finished gathering candidates, has checked all pairs against one another, and has found a connection for all components.
     * @property {number} [FAILED=4] The ICE candidate has checked all candidates pairs against one another and has failed to find compatible matches for all components of the connection. It is, however, possible that the ICE agent did find compatible connections for some components.
     * @property {number} [DISCONNECTED=5] Checks to ensure that components are still connected failed for at least one component of the RTCPeerConnection. This is a less stringent test than "failed" and may trigger intermittently and resolve just as spontaneously on less reliable networks, or during temporary disconnections. When the problem resolves, the connection may return to the "connected" state.
     * @property {number} [CLOSED=6] The ICE agent for this RTCPeerConnection has shut down and is no longer handling requests.
     */

    this.MediaStates = {
        NEW: 0,
        CHECKING: 1,
        CONNECTED: 2,
        COMPLETED: 3,
        FAILED: 4,
        DISCONNECTED: 5,
        CLOSED: 6
    };

