/**
 * Mixin function for loading a module into a factory container. The function expects 'this' to be bound
 * to the container that supports an interface like so:
 * ```
 * {
 *     factory: (name: string, factoryFunction: (container) => any) => void
 *     defer: ((data: any) => any) => void
 *  }
 *  ```
 *
 *  @param {Object} module An where the keys are the name of services, and the values are factory functions. Also supports
 *                         a special key `$defer` that allows registering deffered functions.
 */
export default function loadModule(module) {
    for (let service in module) {
        if (module.hasOwnProperty(service)) {
            if (service === '$init') {
                this.defer(module[service]);
            } else {
                this.factory(service, module[service]);
            }
        }
    }
    return this;
}
