import constants from './constants';
import errors from './errors';

/**
 * @name fcs
 * @namespace
 * @param _server
 * @param _globalBroadcaster
 */
export function CoreImpl({
    GlobalBroadcaster: _globalBroadcaster,
    Config: _config,
}) {

    var dev = null,
        pluginVer = null,
        services = {},
        un = null,
        tkn = null,
        authorizationName = null,
        connected = true,
        tokenRealm = null,
        kandyUAT = null,
        DEFAULT_SUBSCRIPTION_EXPIRY_VALUE = 3600,
        MIN_SUBSCRIPTION_EXPIRY_VALUE = 60;


    /**
     * This function creates a new instance of the library. This allows multiple users to be logged in an using
     * the library at the same time.
     *
     * @name fcs#createInstance
     * @function
     * @returns {fcs}
     *
     * @example
     * var fcs2 = fcs.createInstance();
     * fcs2.setup(...);
     * fcs2.call.startCall(...);
     */

    /**
     * This function returns value of async paramater of $.ajax requests
     *
     * @name fcs#isAsync
     * @function
     * @returns {Boolean} true/false
     * @deprecated
     * @since 3.0.0
     *
     * @example
     * fcs.isAsync();
     */
    this.isAsync = function() {
    };

    /**
     * This function sets async option of $.ajax() requests.
     * If It is set to false, ajax requests will be sent synchronously
     * otherwise ajax requests will be sent asynchronously.
     *
     * @name fcs#setAsync
     * @function
     * @param {Boolean} value
     * @return {Boolean} true/false
     * @deprecated
     * @since 3.0.0
     *
     * @example
     * fcs.setAsync(false);
     */
    this.setAsync = function() {
    };

    /**
     * This function returns username of authenticated user in user@domain format.
     *
     * @name fcs#getUser
     * @function
     * @returns {string} Username of current user
     * @since 3.0.0
     *
     * @example
     * fcs.getUser();
     */
    this.getUser = function(){
        return un;
    };

    /**
     * This function returns authorization name of authenticated user
     *
     * @name fcs#getAuthUser
     * @function
     * @returns {String} Authorization name of current user
     * @since 3.0.0
     *
     * @example
     * fcs.getAuthUser();
     */
    this.getAuthUser = function() {
        if (authorizationName) {
            return authorizationName;
        } else {
            return un;
        }
    };

    /**
     * This function returns current domain name of authenticated user
     *
     * @name fcs#getDomain
     * @function
     * @returns {string} Current domain name
     * @since 3.0.0
     *
     * @example
     * fcs.getDomain();
     */
    this.getDomain = function() {
        return un.split('@')[1];
    };

    /**
     * This function returns the version of the JSL-API
     *
     * @name fcs#getVersion
     * @function
     * @returns {string} Version of the JSL-API
     * @since 3.0.0
     *
     * @example
     * fcs.getVersion();
     */
    this.getVersion = function() {
        return '@{spidr.jsl.version}';
    };

    /**
     * This fucntion returns current device.
     *
     * @name fcs#getDevice
     * @function
     * @returns {string} Device specified for communicating with the server
     * @since 3.0.0
     *
     * @example
     * fcs.getDevice();
     */
    this.getDevice = function() {
        return dev;
    };

    /**
     * This function sets the user as authentication mode and cancels device authentication (if such exists),
     * as user and device modes are mutually exclusive.
     * authname parameter is optional.
     *
     * @name fcs#setUserAuth
     * @function
     * @param {string} user User name to be used for communicating with the server
     * @param {string} password Password to be used for communicating with the server
     * @param {string} authname If provided authname is used instead of user name for authentication
     *
     * @since 3.0.0
     *
     * @example
     * fcs.setUserAuth("Username", "Password","Authname");
     */
    this.setUserAuth = function(user, password, authname) {
        un = user;
        dev = null;
        var data = {
            'username': user,
            'password': password
        };
        if ((typeof authname === 'string') && (authname.trim().length > 0)) {
            data.authname = authname;
            authorizationName = authname;
        } else {
            authorizationName = null;
        }
        _globalBroadcaster.publish(constants.EVENT.BASIC_AUTH_STARTED, data);
    };

    /**
     * This function sets the user as token mode authentication and cancels user authentication or/and device authentication (if such exists),
     * token authentication has priority over other authentications
     *
     * @name fcs#setTokenAuth
     * @function
     * @param {string} user to be used for communicating with the server
     * @param {string} token to be used for communicating with the server
     *
     * @since 3.0.0
     *
     * @example
     * fcs.setTokenAuth("Username", "Token");
     */
    this.setTokenAuth = function(user, token) {
        un = user;
        tkn = token;
        _globalBroadcaster.publish(constants.EVENT.TOKEN_AUTH_STARTED, {
            'username': user,
            'token': token
        });
    };

    this.getTokenAuth = function() {
        return tkn;
    };

    /**
     * This function sets the device as authentication mode and cancels user authentication (if such exists),
     * as user and device modes are mutually exclusive.
     *
     * @name fcs#setDeviceAuth
     * @function
     * @since 3.0.0
     * @param {string} deviceID The device to be used for communicating with the server
     *
     * @example
     * fcs.setDeviceAuth("DeviceID");
     */
    this.setDeviceAuth = function(deviceID) {
        dev = deviceID;
        un = null;
    };

    /**
     * This function sets the authentication realm for time limited token authentication.
     *
     * @name fcs.setRealm
     * @function
     * @since 3.0.4
     * @param {string} realm The realm for the time limited token auth
     *
     * @example
     * fcs.setRealm("realmname");
     */
    this.setRealm = function(realm) {
        tokenRealm = realm;
    };

    /**
     * This function gets the authentication realm for time limited token authentication.
     *
     * @name fcs.getRealm
     * @function
     * @since 3.0.4
     * @returns {string} The realm for the time limited token auth
     *
     * @example
     * var realm = fcs.getRealm();
     */
    this.getRealm = function() {
        return tokenRealm;
    };

    /**
     * This function sets the authentication UAT for kandy Authentication.
     *
     * @name fcs.setKandyUAT
     * @function
     * @since 3.0.4
     * @param {string} uat The User Access Token
     *
     * @example
     * fcs.setKandyUAT("uat");
     */
    this.setKandyUAT = function(uat) {
        kandyUAT = uat;
    };

    /**
     * This function gets the authentication UAT for kandy Authentication.
     *
     * @name fcs.getKandyUAT
     * @function
     * @since 3.0.4
     * @returns {string} The User Access Token
     *
     * @example
     * var uat = fcs.getKandyUAT();
     */
    this.getKandyUAT = function() {
        return kandyUAT;
    };

    /**
     * List of Authentication Types.
     * @see setDeviceAuth
     * @see setUserAuth
     * @name fcs#AuthenticationType
     * @property {number} USER User authentication
     * @property {number} DEVICE Device authentication
     * @readonly
     */
    this.AuthenticationType = {
        USER: 1,
        DEVICE: 2
    };

    /**
     * List of Error Types
     *
     * @name fcs#Errors
     * @property {number} NETWORK Network failures
     * @property {number} AUTH Authentication / Authorization failures
     * @property {number} STATE Invalid state
     * @property {number} PRIV Privilege failures
     * @property {number} UNKNOWN Unknown failures
     * @property {number} LOGIN_LIMIT Login limit exceeded
     * @property {number} INCORRECT_LOGIN_PASS Incorrect identifier
     * @property {number} INVALID_LOGIN Invalid username
     * @property {number} TOKEN_NOT_FOUND Token provided is not valid
     * @property {number} SESSION_EXPIRED Session generated from token is expired
     * @property {number} VIDEO_SESSION_NOT_AVAILABLE Video Session is not available
     * @property {number} PENDING_REQUEST There is a pending request.
     * @property {number} NOT_ALLOWED_SERVICE Service is not allowed.
     * @property {number} NOT_ALLOWED_METHOD Method is not allowed.
     * @property {number} NOT_ALLOWED_INSTANCE Instance is not allowed.
     * @property {number} INVALID_PARAMETER Parameter is invalid.
     * @property {number} CONNECTION_ISSUE Connection problem.
     * @property {number} MEDIA_NOT_FOUND Media not found error.
     * @property {number} MEDIA_NOT_ALLOWED Media not allowed error.
     * @property {number} CALL_FAILED Call failed.
     * @property {number} CALL_ENDED Call ended.
     * @property {number} MAXIMUM_ENTRIES Maximum entries error.
     * @property {number} ENTRY_ALREADY_EXISTS Entry already exists error.
     * @property {number} SERVICE_NOT_AUTHORIZED Service authorization error.
     * @property {number} DB_EXCEPTION Database exception Error.
     * @property {number} INSUFFICIENT_INFO Insufficient error.
     * @property {number} NOT_FOUND Not Found error.
     * @property {number} INTERNAL_SERVER_ERROR Internal server  error.
     * @property {number} SERVICE_UNAVAILABLE Service unavaible error.
     * @property {number} SESSION_NOT_FOUND The device's session was not found.
     * @property {number} CANNOT_SUBSCRIBE Device cannot subscribe.
     * @property {number} MEDIA_REQUIRED The call media to use was not specified.
     *
     * @readonly
     * @example
     * if (e === fcs.Errors.AUTH)
     * {
     *     console.log("Authentication error occured")
     * }
     */
    this.Errors = errors;

    /**
     * @typedef modifiedValues
     * @type Object
     * @property {number} url Modified url to replace xhr original url
     * @property {object} headers Mofied headers object to replace original xhr headers
     * @since 3.1.2
     */

    /**
     * Ajax hook to intercept outgoing xhr request to modify url and headers
     *
     * @since 3.1.2
     *
     * @callback ajaxHook
     * @param {Object} xhr XMLHttpRequest object
     * @param {window} window window object
     * @param {object} params object containing parameters
     * @param {string} [params.type] The HTTP method to use, such as "GET", "POST", "PUT", "DELETE", etc
     * @param {string} [params.url] The URL to send the request to
     * @param {string} [params.headers] Request headers
     * @param {string} [params.data] Request body
     *
     * @return {modifiedValues} object containing modified url and headers
     *
     */

    /**
     * Establishes bandwidth control functionalities based on provided audio codecs and values
     * @typedef opusConfig
     * @type Object
     *
     * @property {string} [maxPlaybackRate] value between 8000 and 48000
     * @property {string} [maxAverageBitrate] value between 6000 and 510000
     * @property {string} [fec] 0 or 1
     * @property {string} [dtx] 0 or 1
     * @property {string} [ptime] integer value between 2.5 and 120
     *
     * @example
     *
     * opusConfig: {
     *      maxPlaybackRate: 16000,
     *      maxAverageBitrate: 20000,
     *      fec: 1,
     *      dtx: 1,
     *      ptime: 40
     * }
     */

    /**
     * Configures plugin mode (as 'webrtc' or 'auto') and h264 status as browser-specific with version restriction (for Chrome and Firefox) or as general default values.
     * @typedef {Object} fcsSetupConfigParamsPluginMode

     * @property {string} [mode="auto"] General plugin mode. 'webrtc' for default webrtc plugin, 'auto' for the usage of native chrome and firefox or the usage of default webrtc plugin for the others.
     * @property {boolean} [h264=true] General H264 codec status.
     * @property {object} [chrome] Chrome-specific configurations
     * @property {string} [chrome.mode] Chrome-specific plugin mode. Overrides the general one.
     * @property {boolean} [chrome.h264] Chrome-specific H264 codec status. Overrides the general one.
     * @property {string} [chrome.version] Version lowerbound for Chrome configurations. Ex: "40+". Includes all the versions if not given.
     * @property {object} [firefox] Firefox-specific configurations
     * @property {string} [firefox.mode] Firefox-specific plugin mode. Overrides the general one.
     * @property {boolean} [firefox.h264] Firefox-specific H264 codec status. Overrides the general one.
     * @property {string} [firefox.version] Version lowerbound for Firefox configurations. Ex: "40+". Includes all the versions if not given.
     *
     * @example
     *
     * pluginMode: {
     *     mode: 'webrtc',
     *     h264: true,
     *     chrome: {
     *         mode: 'auto'
     *     },firefox: {
     *         version: '38+',
     *         mode: 'auto'
     *     }
     * }
     */

    /**
     * Array of servers to be tried to for notification subscription.
     * @typedef {Object[]} fcsSetupConfigParamsServers
     *
     * @property {string} [protocol=window.location.protocol] HTTP protocol to be used. Ex: Http, Https
     * @property {string} [restUrl=window.location.hostname] The URL of REST server http://ip:port.
     * @property {string} [restPort=window.location.port] The port of REST server http://ip:port.
     * @property {string} [websocketProtocol=ws] Determines if the websocketProtocol is secure or non-secure.
     * @property {string} [websocketIP=window.location.hostname] Holds the websocket connection's IP adress.
     * @property {string} [websocketPort=8581] Holds the websocket connection's port value. By defult, it is 8581.
     *
     * @example
     * "servers": [{
     *     "protocol": "https",
     *     "restUrl": "spidrdomain.com",
     *     "restPort": "443",
     *     "websocketProtocol": "wss",
     *     "websocketIP": "spidrdomain.com",
     *     "websocketPort": "443"
     * }],
     */

    /**
     * A handler function that is given a chance to make modificaitons to a given SDP before and/or after a local and/or remote description is set.
     *
     * @callback sdpHandler
     * @param {Object} params The object of parameters for the function.
     * @param {Object} params.currentSdp The current SDP provided by the last handler which may be modified from the original SDP. You can check if currentSdp has been modified by comparing it against originalSdp for referential equality. You cannot modify this object.
     * @param {Object} params.originalSdp The original SDP provided to the pipeline of handlers. You cannot modify this object.
     * @param {string} params.operation The call operation being done that requires the SDP. Values are in fcs.SDP_CONSTANTS.OPERATION.
     * @param {string} params.step The step at which the pipeline is being called. Values are in fcs.SDP_CONSTANTS.STEP.
     * @param {string} params.type The type of sdp. Values are in fcs.SDP_CONSTANTS.TYPE.
     * @param {string} params.callId The Id of the call this Sdp is for. It may be undefined if this is a start call operation since it will not have an Id yet.
     * @param {function} params.next Handlers should return the value of calling `next` with the modified SDP as its only parameter. If no modifications are made, the handler should return the value of calling next with `currentSdp`. Not calling `next` will force the pipeline to ignore the rest of the handlers.
     * @example
     * ``` javascript
     * function forceH264({next, currentSDP, originalSDP, operation, step, type, callId}) {
     *     const nextSDP = Object.assign({}, currentSDP, {propertyName: 'value'});
     *
     *     return next(nextSDP);
     *
     *     // or, if you want to end the pipeline
     *     return nextSDP;
     * }
     * ```
     */

    /**
     * JSL library setup
     * @typedef {Object} fcsSetupConfigParams
     * @property {string} [notificationType=LONGPOLLING] The notification type to be used {@link fcs.notification#NotificationTypes}
     * @property {string} [serverRetryNumber=5] Server retry number is used when websocket failure. The current websocket will be tried for the given parameter (Between 0 and 10)
     * @property {string} [serverRetryInterval] Server retry time interval is the current failed websocket retry interval. Default value is 5000. (Between 1000 and 10000)
     * @property {Object[]} [servers=[]] servers config {@link fcsSetupConfigParamsServers}.
     * @property {string} [restUrl] Deprecated. please use {@link fcsSetupConfigParamsServers}
     * @property {string} [restPort] Deprecated. please use {@link fcsSetupConfigParamsServers}
     * @property {string} [websocketProtocol] Deprecated. please use {@link fcsSetupConfigParamsServers}
     * @property {string} [websocketIP] Deprecated. please use {@link fcsSetupConfigParamsServers}
     * @property {string} [websocketPort] Deprecated. please use {@link fcsSetupConfigParamsServers}
     * @property {string} [protocol] Deprecated. please use {@link fcsSetupConfigParamsServers}
     * @property {string} [polling=30] Polling time value in seconds.
     * @property {number} [expires=3600] Expire time value in seconds.Default value is 3600.(60  and bigger than 60 ).
     * @property {string} [codecsToRemove] Audio codesc to be removed.
     * @property {string} [codecsToRemoveForVideo] Video codesc to be removed.
     * @property {Object} [opusConfig] Bandwidth controls to add for Opus audio codec
     * @property {string} [callAuditTimer=30000] Audit time value for calls.
     * @property {boolean} [cors=false] True if Cross-Origin Request Sharing supported.
     * @property {string} [services="IM, Presence, Call"] Defines the enabled services for client.
     * @property {boolean} [serverProvidedTurnCredentials=false] Provide TURN server credentials from server or not.
     * @property {number} [iceCandidateCollectionTimeoutInterval=3000] When provided (in milliseconds), ice candidate collection assumed to be completed if at least one candidate is received within the interval.
     * @property {number} [relayCandidateCollectionTimeoutCycle] When provided, iceCandidateCollectionTimeoutInterval is restarted until receiving first relay candidate. if the provided cycle limit is reached, ice candidate collection assumed to be completed.
     * @property {boolean} [useRelayOnly] Forces the ICE Agent to only use relay candidate by setting the iceTransportPolicy to relay
     * @property {Object} [pluginMode] plugin mode config {@link fcsSetupConfigParamsPluginMode}.
     * @property {string} [connectivityInterval=10000] Connectivity check interval time value. If it is 0 connectivity check is diabled. Recommended to use values greater than 10000, due to performance issues.
     * @property {string} [connectivityLostRetry=false] Connectivity check will retry when the connectivity fails until it succeed on the following default patern 10s, 20s, 40s, 80s, ... 640s, 640s, ...
     * @property {string} [connectivityRetryStart=10000] Initial time (ms) after the connectity is retried.
     * @property {string} [connectivityRetryLimit=640000] Maximum time (ms) between retries.
     * @property {string} [connectivityRetryMultiplier=2] Multiplier that will be applied for the next retry.
     * @property {string} [websocketInterval=10000] Websocket health check interval. If the websocketInterval parameter value is 0, websocket health check will be disabled.
     * @property {string} [backgroundWebsocketInterval=60000] Websocket health check interval for mobile when the device is running in the background.
     * @property {string} [pluginLogLevel="2"] The log level of webrtc plugin
     * @property {string} [staggeredTimerMin="300"] Minimum value for staggered timer. (Timer used to prevent all devices to reconnect at the same time)
     * @property {string} [staggeredTimerMax="5000"] Maximum value for staggered timer.
     * @property {string} [videoContainer] html node in which to inject the video
     * @property {string} [remoteVideoContainer] html node in which to inject the remote video
     * @property {string} [localVideoContainer] html node in which to inject the preview of the user camera
     * @property {string} [iceserver=""] ice server ip address
     * @property {boolean} [webrtcdtls=false] webrtc dtls status
     * @property {boolean} [recordCallStats=false] When enabled, call statistics are recorded in app's localstorage after the call is terminated.
     * @property {boolean} [dscpEnabled=false] Enabled experimental DSCP markings on supported platforms (Chrome only).
     * @property {string} [language="en"] language setting of the plugin
     * @property {ajaxHook} [ajaxHook] ajax hook to intercept outgoing xhr request to modify url and headers
     * @property {boolean} [callReplace=false] When enabled, inform Spidr that replaceCall is supported
     * @property {boolean} [ringingFeedback=false] When enabled, inform Spidr that RingingFeedback is supported
     * @property {boolean} [trickleIceSupport="none"] Specifies trickle ice support. "none" is default value and trickle ice isn't supported. "half" means half trickle is supported. "full" means full trickle is supported.
     * @property {string} [cachePrefix=""] Allows the user to specify a unique prefix for keys stored in localStorage.
     * @property {boolean} [forceDisableMediaOnHold=false] Disables any type of media (e.g. Comfort Noise) from transmitting when call is held locally.
     * @property {boolean} [videoInactiveOnHold=false] Sets the video as "inactive" instead of "sendonly" when holding a call.
     * @property {boolean} [performVideoStartWorkaround=false] Enable the video start workaround for Chrome < 63, now disabled by default.
     * @property {sdpHandler[]} [sdpHandlers=[]] The array of handlers for SDP manipulations.
     * @property {string} [bundlePolicy=DISABLED] The bundle policy to use for peer connections. Value can be fcs.SDP_CONSTANTS.BUNDLE_POLICY.MAX_COMPAT, fcs.SDP_CONSTANTS.BUNDLE_POLICY.MAX_BUNDLE,  fcs.SDP_CONSTANTS.BUNDLE_POLICY.BALANCED or fcs.SDP_CONSTANTS.BUNDLE_POLICY.DISABLED. The DISABLED option means that bundle group lines will be removed from every SDP.
     * @property {Object} [callConstraints] Custom RTCPeerConnection constraints to use for calls. Will cause errors if malformed.
     * @property {Object} [callConstraints.chrome] Custom constraints to be used on Google Chrome.
     * @property {Object} [callConstraints.firefox] Custom constraints to be used on Mozilla Firefox.
     * @property {Object} [callConstraints.plugin] Custom constraints to be used with the WebRTC Plugin.
     * @property {boolean} [callResyncOnConnect=false] Resync all active call states upon connecting (requires Kandy Link 4.7.1+).
     */

    /**
     * This function is used to set up JSL library
     *
     * @name fcs#setup
     * @function
     * @param {fcsSetupConfigParams} config configuration parameters
     * @since 3.0.0
     * @example
     *
     * fcs.setup(
     *   {
     *       notificationType: fcs.notification.NotificationTypes.WEBSOCKET,
     *       "servers": [{
     *           "protocol": "https",
     *           "restUrl": "spidrdomain.com",
     *           "restPort": "443",
     *           "websocketProtocol": "wss",
     *           "websocketIP": "spidrdomain.com",
     *           "websocketPort": "443"
     *       }],
     *       callAuditTimer: 30000,
     *       clientControlled : true,
     *       pluginMode: {
     *          mode: 'webrtc',
     *          h264: true,
     *          chrome: {
     *              mode: 'auto'
     *          },
     *          firefox: {
     *              version: '38+',
     *              mode: 'auto'
     *          }
     *       },
     *       connectivityInterval: '20000',
     *       "iceserver": [{
     *           "urls": "turns:turn1.spidrdomain.com:443?transport=tcp",
     *           "credential": ""
     *           }, {
     *              "urls": "turns:turn2.spidrdomain.com:443?transport=tcp",
     *              "credential": ""
     *       }],
     *       webrtcdtls: true,
     *       recordCallStats: true,
     *       videoContainer: document.getElementById("defaultVideoContainer")
     *   }
     * );
     *
     * // example for multiple server
     *
     * fcs.setup(
     *   {
     *       notificationType: fcs.notification.NotificationTypes.WEBSOCKET_ONLY,
     *       servers: [{protocol: 'http',
     *                   restUrl: '1.1.1.1',
     *                   restPort: '8580',
     *                   websocketProtocol: 'ws',
     *                   websocketIP: '1.1.1.1',
     *                   websocketPort: '8578'},
     *                  {protocol: 'http',
     *                   restUrl: '1.1.1.1',
     *                   restPort: '8580',
     *                   websocketProtocol: 'ws',
     *                   websocketIP: '1.1.1.1',
     *                   websocketPort: '8581'}],
     *      serverRetryNumber: '2',
     *      serverRetryInterval: '3000',
     *       pluginMode: {
     *          mode: 'webrtc',
     *          h264: true,
     *          chrome: {
     *              mode: 'auto'
     *          },
     *          firefox: {
     *              version: '38+',
     *              mode: 'auto'
     *          }
     *       },
     *       connectivityInterval: '20000'
     *       websocketInterval: '20000',
     *       iceserver: [{"urls":"stun:206.165.51.69:3478"},
     *                     {"urls":"turn:206.165.51.69:3478",
     *                       "credential":"dummyCredential",
     *                       "password":"dummyPwd"}]
     *       webrtcdtls: true,
     *       recordCallStats: true,
     *       videoContainer: document.getElementById("defaultVideoContainer")
     *   }
     * );
     *
     *
     */
    this.setup = function(configParams, delayInitMedia) {
        delayInitMedia = delayInitMedia === undefined ? false : delayInitMedia;
        var param, i, j, codecsToReplaceDefaultFound, paramFields;
        for (param in configParams) {
            if (configParams.hasOwnProperty(param)) {
                if (param === 'codecsToReplace') {
                    if (typeof _config[param] === 'undefined') {
                        _config[param] = [];
                    }
                    for (i in configParams[param]) {
                        if (configParams[param].hasOwnProperty(i)) {
                            codecsToReplaceDefaultFound = false;
                            for (j in _config[param]) {
                                if (_config[param].hasOwnProperty(j) &&
                                    _config[param][j].name === configParams[param][i].name) {
                                    codecsToReplaceDefaultFound = true;
                                    _config[param][j].value = configParams[param][i].value;
                                }
                            }

                            if (!codecsToReplaceDefaultFound) {
                                _config[param].push(configParams[param][i]);
                            }
                        }
                    }
                } else if (param === 'pluginMode') {
                    paramFields = Object.keys(configParams[param]);
                    for (i in paramFields) {
                        if (paramFields.hasOwnProperty(i)) {
                            _config[param][paramFields[i]] = configParams[param][paramFields[i]];
                        }
                    }
                } else {
                    _config[param] = configParams[param];
                }
            }
        }

        // This function checks the expiration date.
        // It should not be a very small value.
        // Otherwise, the api enters the loop.There is no upper limit.
        var handleValidSubscriptionExpiryTimerValue = function(time) {
            if (time && parseInt(time) >= MIN_SUBSCRIPTION_EXPIRY_VALUE) {
                return time;
            } else {
                return DEFAULT_SUBSCRIPTION_EXPIRY_VALUE;
            }
        };

        _config.trickleIceSupport = (Object.values(constants.TRICKLE).indexOf(_config.trickleIceSupport) !== -1) ? _config.trickleIceSupport : 'none';
        _config.expires = handleValidSubscriptionExpiryTimerValue(configParams ? configParams.expires : undefined);
        if (!delayInitMedia) {
            _globalBroadcaster.publish(constants.EVENT.FCS_SETUP_COMPLETED, _config);
        }
    };

    // This function can be used to set conenction info parameters into the config
    this.setConnectionInfo = function (connectionParams) {
        for (var param in connectionParams) {
            if (connectionParams.hasOwnProperty(param)) {
                _config[param] = connectionParams[param];
            }
        }
    };

    /**
     * This is an object of constants for advanced use only. Use these constants in your SDP handlers. See fcs.setup() to see how to set SDP handlers.
     *
     * @name fcs#SDP_CONSTANTS
     * @property {Object} SDP_CONSTANTS The object of constants for use in SDP handlers.
     * @property {Object} SDP_CONSTANTS.TYPE The type of SDP.
     * @property {string} SDP_CONSTANTS.TYPE.OFFER Offer sdp type.
     * @property {string} SDP_CONSTANTS.TYPE.ANSWER Answer sdp type.
     * @property {string} SDP_CONSTANTS.TYPE.PRANSWER Pranswer sdp type.
     * @property {Object} SDP_CONSTANTS.OPERATION The operation for which the sdp will be used.
     * @property {string} SDP_CONSTANTS.OPERATION.START_CALL A call start operation.
     * @property {string} SDP_CONSTANTS.OPERATION.ANSWER_CALL A call answer operation.
     * @property {string} SDP_CONSTANTS.OPERATION.HOLD A hold operation.
     * @property {string} SDP_CONSTANTS.OPERATION.UPDATE An update operation.
     * @property {string} SDP_CONSTANTS.OPERATION.REOFFER A reoffer operation.
     * @property {string} SDP_CONSTANTS.OPERATION.RESPOND A respond operation.
     * @property {string} SDP_CONSTANTS.OPERATION.DATA_CHANNEL A data channel operation.
     * @property {Object} SDP_CONSTANTS.STEP The step in the process that the SDP is at.
     * @property {string} SDP_CONSTANTS.STEP.PRE_SET_LOCAL The local SDP is about to be set.
     * @property {string} SDP_CONSTANTS.STEP.PRE_SET_REMOTE The remote SDP is about to be set.
     * @property {string} SDP_CONSTANTS.STEP.PRE_SEND_LOCAL The local SDP is about to be sent to the other peer.
     */
    this.SDP_CONSTANTS = constants.WEBRTC.SDP;

    /**
     * Plugin required event will be fired in case that plug-in not found or version is outdated
     *
     * @name fcs#onPluginRequired
     * @event
     * @param {fcs.call#MediaErrors} event media initialization error
     * @since 3.0.0
     * @example
     *
     * fcs.onPluginRequired = function(error) {
     *     switch (error) {
     *         case fcs.call.MediaErrors.WRONG_VERSION:
     *             // Media Plugin Version Not Supported
     *             // Download plugin
     *             break;
     *         case fcs.call.MediaErrors.NEW_VERSION_WARNING:
     *             // New Media Version Available Wanrning
     *             break;
     *         case fcs.call.MediaErrors.NOT_INITIALIZED:
     *             // Media couldn't be initialized
     *             break;
     *         case fcs.call.MediaErrors.NOT_FOUND:
     *             // Plugin couldn't be found!
     *             // Download plugin
     *             break;
     *     }
     * }
     */

    /**
     * This function sets version of plugin
     *
     * @name fcs#setPluginVersion
     * @function
     * @param {string} version
     * @since 3.0.0
     * @example
     *
     * fcs.setPluginVersion(version);
     */
    this.setPluginVersion = function(version) {
        pluginVer = version;
    };

    /**
     * This function returns version of plugin
     *
     * @name fcs#getPluginVersion
     * @function
     * @returns {String} Version of Current Plugin
     * @since 3.0.0
     * @example
     *
     * fcs.getPluginVersion();
     */
    this.getPluginVersion = function() {
        return pluginVer;
    };

    /**
     * This function returns assigned services of authenticated user.
     *
     * @name fcs#getServices
     * @function
     * @returns {object} The assigned services of authenticated user
     * @since 3.0.0
     * @example
     *
     * fcs.getServices();
     */
    this.getServices = function() {
        return services;
    };

    /**
     * This function assigns determined services to current user
     *
     * @name fcs#setServices
     * @function
     * @param {array} serviceParam The list of assigned services for the user
     * @since 3.0.0
     * @example
     * fcs.setServices(["CallControl", "RestfulClient"]);
     */
    this.setServices = function(serviceParam) {
        var i;
        // for each element in serviceParam array, we create the service with value "true" in "services" object
        if (serviceParam) {
            services = {};
            for (i = 0; i < serviceParam.length; i++) {
                switch (serviceParam[i]) {
                case 'CallDisplay':
                    services.callDisplay = true;
                    break;
                case 'CallDisposition':
                    services.callDisposition = true;
                    break;
                case 'RestfulClient':
                    services.restfulClient = true;
                    break;
                case 'call':
                    services.callControl = true;
                    services.remoteCallControl = false;
                    break;
                case 'CallControl':
                    services.callControl = true;
                    services.remoteCallControl = false;
                    break;
                case 'RCC':
                    services.callControl = false;
                    services.remoteCallControl = true;
                    break;
                case 'CallMe':
                    services.callMe = true;
                    break;
                case 'Directory':
                    services.directory = true;
                    break;
                case 'ClickToCall':
                    services.clickToCall = true;
                    break;
                case 'Presence':
                    services.presence = true;
                    break;
                case 'AddressBook':
                    services.contacts = true;
                    break;
                case 'CallLog':
                    services.history = true;
                    break;
                case 'Custom':
                    services.custom = true;
                    break;
                case 'IM':
                    services.IM = true;
                    break;
                case 'Route':
                    services.routes = true;
                    break;
                case 'DataChannel':
                    services.dataChannel = true;
                    break;
                default:
                    break;
                }
            }
        }
    };

    /**
     * Returns network connectivity status.
     *
     * @name fcs#isConnected
     * @function
     *
     * @returns {Boolean}, true if connection is up otherwise false.
     */
    this.isConnected = function() {
        return connected;
    };

    this.setConnected = function(connectionStatus) {
        connected = connectionStatus === true ? true : false;
    };
}
