import * as eventTypes from './eventTypes'
import * as actionTypes from './actionTypes'

/**
 * Helper function for call error events.
 * @method callErrorEvent
 * @param  {Object} action
 * @return {Object}
 */
function callErrorEvent (action) {
  if (action.error) {
    return {
      type: eventTypes.CALL_ERROR,
      args: { callId: action.payload.callId, error: action.payload }
    }
  }
}

var events = {}

// Actions that only emit an event on an error action.
// For most, callStateChange emits the success event.
events[actionTypes.IGNORE_CALL_FINISH] = callErrorEvent
events[actionTypes.REJECT_CALL_FINISH] = callErrorEvent
events[actionTypes.END_CALL_FINISH] = callErrorEvent
events[actionTypes.START_VIDEO_FINISH] = callErrorEvent
events[actionTypes.STOP_VIDEO_FINISH] = callErrorEvent
events[actionTypes.HOLD_CALL_FINISH] = callErrorEvent
events[actionTypes.UNHOLD_CALL_FINISH] = callErrorEvent
events[actionTypes.SEND_DTMF_FINISH] = callErrorEvent
events[actionTypes.SEND_CUSTOM_PARAMETERS_FINISH] = callErrorEvent

events[actionTypes.MAKE_CALL_FINISH] = function (action) {
  if (action.error) {
    // Call start error event.
    return callErrorEvent(action)
  } else {
    // Call start event.
    return {
      type: eventTypes.CALL_STARTED,
      args: { callId: action.payload.callId }
    }
  }
}

events[actionTypes.CALL_INCOMING] = function (action) {
  return {
    type: eventTypes.CALL_INCOMING,
    args: { callId: action.payload.callId }
  }
}

events[actionTypes.FORWARD_CALL_FINISH] = function (action) {
  if (action.error) {
    return callErrorEvent(action)
  } else {
    return {
      type: eventTypes.CALL_FORWARDED,
      args: { callId: action.payload.callId }
    }
  }
}

events[actionTypes.CALL_STATE_CHANGE] = function (action) {
  return {
    type: eventTypes.CALL_STATE_CHANGE,
    args: {
      callId: action.payload.callId,
      state: action.payload.state,
      transition: action.payload.transition
    }
  }
}

events[actionTypes.CALL_MEDIA_STATE_CHANGE] = function (action) {
  return {
    type: eventTypes.CALL_MEDIA_STATE_CHANGE,
    args: {
      callId: action.payload.callId,
      mediaState: action.payload.mediaState
    }
  }
}

events[actionTypes.ANSWER_CALL_FINISH] = function (action) {
  // For a successful answer call, an event is emitted
  //      as a stateChanged event.
  if (action.error) {
    // Call start error event.
    return callErrorEvent(action)
  }
}

events[actionTypes.JOIN_CALL_FINISH] = function (action) {
  if (action.error) {
    return callErrorEvent(action)
  } else {
    return {
      type: eventTypes.CALL_JOIN,
      args: {
        callId: action.payload.callId,
        joinedCalls: action.payload.params.joinedCalls
      }
    }
  }
}

events[actionTypes.MUTE_CALL_FINISH] = function (action) {
  if (action.error) {
    return callErrorEvent(action)
  } else {
    return {
      type: eventTypes.CALL_STATE_CHANGE,
      args: { callId: action.payload.callId }
    }
  }
}

events[actionTypes.UNMUTE_CALL_FINISH] = function (action) {
  if (action.error) {
    return callErrorEvent(action)
  } else {
    return {
      type: eventTypes.CALL_STATE_CHANGE,
      args: { callId: action.payload.callId }
    }
  }
}

events[actionTypes.SILENCE_CALL_FINISH] = function (action) {
  if (action.error) {
    return callErrorEvent(action)
  } else {
    return {
      type: eventTypes.CALL_STATE_CHANGE,
      args: { callId: action.payload.callId }
    }
  }
}

events[actionTypes.UNSILENCE_CALL_FINISH] = function (action) {
  if (action.error) {
    return callErrorEvent(action)
  } else {
    return {
      type: eventTypes.CALL_STATE_CHANGE,
      args: { callId: action.payload.callId }
    }
  }
}

events[actionTypes.START_SCREENSHARE_FINISH] = function (action) {
  if (action.error) {
    return callErrorEvent(action)
  } else {
    return {
      type: eventTypes.CALL_SCREENSHARE_CHANGE,
      args: { callId: action.payload.callId }
    }
  }
}

events[actionTypes.STOP_SCREENSHARE_FINISH] = function (action) {
  if (action.error) {
    return callErrorEvent(action)
  } else {
    return {
      type: eventTypes.CALL_SCREENSHARE_CHANGE,
      args: { callId: action.payload.callId }
    }
  }
}

// TODO: Store devices in state on device change
// Notify an application when media devices change.
events[actionTypes.CHECK_DEVICES_FINISH] = function (action) {
  if (!action.error) {
    return {
      type: eventTypes.DEVICE_CHANGE,
      args: { devices: action.payload }
    }
  } else {
    return {
      type: eventTypes.DEVICE_ERROR,
      args: {
        devices: {},
        error: action.payload
      }
    }
  }
}

events[actionTypes.SET_DEFAULT_DEVICES_FINISH] = function (action) {
  if (action.error) {
    return {
      type: eventTypes.DEVICE_ERROR,
      args: { error: action.payload }
    }
  } else {
    return {
      type: eventTypes.DEVICE_DEFAULT_CHANGE,
      args: { devices: action.payload }
    }
  }
}

events[actionTypes.PROMPT_USER_MEDIA_FINISH] = function (action) {
  if (action.error) {
    return {
      type: eventTypes.MEDIA_PERMISSIONS,
      args: {
        granted: false,
        devices: action.payload.options,
        error: action.payload.error
      }
    }
  } else {
    return {
      type: eventTypes.MEDIA_PERMISSIONS,
      args: {
        granted: true,
        devices: action.payload
      }
    }
  }
}

// TODO: Store devices in state on device change
events[actionTypes.CHANGE_SPEAKER_FINISH] = function (action) {
  if (action.error) {
    return {
      type: eventTypes.DEVICE_ERROR,
      args: { error: action.payload }
    }
  } else {
    return {
      type: eventTypes.DEVICE_CHANGE,
      args: {
        type: 'speaker',
        deviceId: action.payload
      }
    }
  }
}

events[actionTypes.INIT_MEDIA_FINISH] = function (action) {
  return {
    type: eventTypes.MEDIA_INITIALIZED,
    args: { result: action.payload }
  }
}

events[actionTypes.START_LOCAL_VIDEO_FINISH] = function (action) {
  if (action.error) {
    return {
      type: eventTypes.LOCAL_VIDEO_ERROR,
      args: { error: action.payload }
    }
  } else {
    return {
      type: eventTypes.LOCAL_VIDEO_CHANGE,
      args: { displaying: true }
    }
  }
}

events[actionTypes.STOP_LOCAL_VIDEO_FINISH] = function (action) {
  if (action.error) {
    return {
      type: eventTypes.LOCAL_VIDEO_ERROR,
      args: { error: action.payload }
    }
  } else {
    return {
      type: eventTypes.LOCAL_VIDEO_CHANGE,
      args: { displaying: false }
    }
  }
}

function audioBridgeEvents (action) {
  if (action.error) {
    return {
      type: eventTypes.BRIDGE_ERROR,
      args: {
        error: action.payload
      }
    }
  } else {
    return {
      type: eventTypes.BRIDGE_CHANGE,
      args: {
        bridgeId: action.payload.bridgeId
      }
    }
  }
}

events[actionTypes.CREATE_AUDIO_BRIDGE_FINISH] = audioBridgeEvents
events[actionTypes.CLOSE_AUDIO_BRIDGE_FINISH] = audioBridgeEvents
events[actionTypes.UPDATE_AUDIO_BRIDGE_CALLS] = audioBridgeEvents
events[actionTypes.MUTE_AUDIO_BRIDGE_FINISH] = audioBridgeEvents
events[actionTypes.UNMUTE_AUDIO_BRIDGE_FINISH] = audioBridgeEvents
events[actionTypes.SILENCE_AUDIO_BRIDGE_FINISH] = audioBridgeEvents
events[actionTypes.UNSILENCE_AUDIO_BRIDGE_FINISH] = audioBridgeEvents

export default events
