/**
 * An outgoing call has successfully started.
 *
 * @public
 * @memberof call
 * @requires call
 * @requires callMe
 * @event call:start
 * @param {Object} params
 * @param {string} params.callId The Id of the call.
 */
export const CALL_STARTED = 'call:start'

/**
 * A new incoming call has been received.
 *
 * @public
 * @memberof call
 * @requires call
 * @event call:receive
 * @param {Object} params
 * @param {string} params.callId The Id of the call.
 * @example
 * client.on('call:receive', function(callId) {
 *     // We have received a call, prompt the user to respond.
 *     promptUser(client.call.getById(callId));
 * });
 */
export const CALL_INCOMING = 'call:receive'

/**
 * Calls have been successfully joined.
 * @public
 * @requires call
 * @memberof call
 * @event call:join
 * @param {Object} params
 * @param {string} params.callId The ID of the new, joined call.
 * @param {Array}  params.joinedCalls The two calls that were joined together.
 */
export const CALL_JOIN = 'call:join'

/**
 * A call has successfully been forwarded.
 *
 * @public
 * @memberof call
 * @requires call
 * @event call:forward
 * @param {Object} params
 * @param {string} params.callId The Id of the call.
 */
export const CALL_FORWARDED = 'call:forward'

/**
 * A call's state has changed.
 *
 * @public
 * @memberof call
 * @requires call
 * @requires callMe
 * @event call:stateChange
 * @param {Object} params
 * @param {string} params.callId The Id of the call.
 * @param {string} params.state New state of the call.
 * @param {Object} params.transition Information about the state change.
 * @param {number} params.transition.code Code representing the reason for the state change.
 * @param {string} params.transition.reasonText Explanation of the status code.
 * @param {string} params.transition.oldState The previous state of the call.
 * @example
 * client.on('call:stateChange', function(params) {
 *     // We are now in call with another user, so update our app to show it.
 *     if(params.state === client.call.states.IN_CALL) {
 *         renderCall(client.call.getById(params.callId));
 *     }
 * });
 */
export const CALL_STATE_CHANGE = 'call:stateChange'

/**
 * A call's media state has changed.
 * @public
 * @memberof call
 * @requires call
 * @requires callMe
 * @event call:mediaStateChange
 * @param {Object} params
 * @param {string} params.callId The Id of the call.
 * @param {string} params.mediaState New media state of the call.
 */
export const CALL_MEDIA_STATE_CHANGE = 'call:mediaStateChange'

/**
 * Screensharing has been started turned on or off.
 *
 * @public
 * @memberof call
 * @requires call
 * @requires callMe
 * @event call:screenshareChange
 * @param {Object} params
 * @param {string} params.callId The Id of the call.
 */
export const CALL_SCREENSHARE_CHANGE = 'call:screenshareChange'

/**
 * An error has occurred with a call.
 *
 * @public
 * @memberof call
 * @requires call
 * @requires callMe
 * @event call:error
 * @param {Object} params
 * @param {string} params.callId The id of the call.
 * @param {api.BasicError} params.error The Basic error object.
 */
export const CALL_ERROR = 'call:error'

/**
 * The status of previewing local video has changed.
 * @public
 * @memberof media
 * @requires call
 * @event videoPreview:change
 * @param {Object} params
 * @param {boolean} params.displaying Whether the local video preview is being displayed or not.
 */
export const LOCAL_VIDEO_CHANGE = 'videoPreview:change'

/**
 * An error has occurred when changing local video preview status.
 * @public
 * @memberof media
 * @requires call
 * @event videoPreview:error
 * @param {Object} params
 * @param {api.BasicError} params.error Information about the error.
 */
export const LOCAL_VIDEO_ERROR = 'videoPreview:error'

// Media devices.

/**
 * A change has been made to default devices used for calls.
 * @public
 * @memberof media
 * @requires call
 * @requires callMe
 * @event devices:defaultsChange
 * @param {Object} params
 * @param {Object} params.devices The devices now set as default.
 */
export const DEVICE_DEFAULT_CHANGE = 'devices:defaultsChange'

/**
 * Available media devices have been changed.
 * @public
 * @memberof media
 * @requires call
 * @requires callMe
 * @event devices:change
 * @param {Object} params
 * @param {Object} params.devices The devices, separated by device type.
 */
export const DEVICE_CHANGE = 'devices:change'

/**
 * Media device permissions have been granted by the user.
 * @memberof media
 * @requires call
 * @event media:permissions
 * @param {Object} params
 * @param {Object} params.devices The devices to request permission for.
 * @param {api.BasicError} params.error The Basic error object.
 * @param {boolean} params.granted Whether permission was granted?
 */
export const MEDIA_PERMISSIONS = 'media:permissions'

/**
 * An error occurred while performing a device operation.
 * @public
 * @memberof media
 * @requires call
 * @requires callMe
 * @param {Object} params
 * @param {api.BasicError} params.error The Basic error object.
 */
export const DEVICE_ERROR = 'devices:error'

/**
 * Media support has been checked.
 * @public
 * @memberof media
 * @requires call
 * @requires callMe
 * @event media:initialize
 * @param {Object} params
 * @param {Object} params.result Results of initializing media.
 * @param {boolean} params.result.error Whether the initialization was successful or not.
 * @param {number} params.result.code A unique code describing the result scenario.
 * @param {string} params.result.message Human readable message of the result.
 */
export const MEDIA_INITIALIZED = 'media:initialize'

/**
 * A change has occurred in an audio bridge.
 * @public
 * @memberof AudioBridge
 * @event audioBridge:change
 * @param {Object} params
 * @param {string} bridgeId The bridge that the change occurred on.
 */
export const BRIDGE_CHANGE = 'audioBridge:change'

/**
 * An error occurred while performing an audio bridge operation.
 * @public
 * @memberof AudioBridge
 * @event audioBridge:error
 * @param {Object} params
 * @param {api.BasicError} params.error The Basic error object.
 */
export const BRIDGE_ERROR = 'audioBridge:error'
