import { cloneDeep } from 'lodash/fp';
import constants from '../constants';

// Our ice candidate timeout logic does not support bundling. There are cases
// where we should be interupting ice candidate collection but restart the
// timeout instead because the vide mline does not have any candidates. In a
// case where the browser is bundling, you would expect that there wouldn't
// be any candidates in the video mline. Removing the bundle line from the SDP
// ensures that we never bundle candidates. This shouldn't be necessary when we
// support trickle ICE since you wouldn't expect the SDP to have all of its
// candidates up front anyways.
export default function createBundleHandler(getConfig) {
    return function bundleHandler({next, currentSdp}) {
        if (getConfig().bundlePolicy !== constants.WEBRTC.SDP.BUNDLE_POLICY.DISABLED || currentSdp.groups === undefined) {
            return next(currentSdp);
        }

        const newSdp = cloneDeep(currentSdp);

        delete newSdp.groups;
        return next(newSdp);
    };
}
