// TODO: Fix this?
/* eslint-disable prefer-promise-reject-errors */

// Import the "Next" version of FCS.
import { createFcs } from 'fcs/next'
import { FCS_CALL_STATES, FCS_ICE_MEDIA_STATES } from '../constants'

import BasicError, { bridgeCodes } from '../../errors'
import { logManager } from '../../logs'
import { v4 as uuidV4 } from 'uuid'

// Get the logger
const log = logManager.getLogger('CALL')

/**
 * Shim for interactions between FCS calls and Redux middleware.
 * Because FCS uses layered callbacks, it becomes either complicated or tedious
 * quite quickly when trying to implement all call features.
 * This shim is meant to remove the callback-bloat from the middleware.
 * It wraps FCS' multiple step async calls into a single step.
 * This also handles the FCS call objects, since they cannot be stored in state.
 * @method shim
 * @param  {Object} context [description]
 * @return {Object} api - Functions wrapped by the shim.
 */
export default function shim (context) {
  const { getUsername } = context
  const fcs = createFcs()

  /**
   * Map of on-going calls in FCS.
   * Keyed by the ID as it exists in the SDK
   * @type {Object}
   */
  var ongoingCalls = {}

  /**
   * Map of audio bridges in FCS.
   * Keyed by the ID as it exists in the SDK
   * @type {Object}
   */
  var audioBridges = {}

  /*
   * Information about the local, non-call video stream.
   */
  var localStream = null

  /**
   * Utility function to retrieve the corresponding SDK Call IDs for
   * a list of FCS call object IDs
   *
   * @param {Array} fcsCalls - FCS call objects
   * @returns {Array} - SDK call ID strings
   */
  function getUnifiedSdkCalls (fcsCalls) {
    const map = {}
    const callIds = []
    if (fcsCalls.length > 0) {
      for (const k in ongoingCalls) {
        map[ongoingCalls[k].getId()] = k
      }
      fcsCalls.forEach(call => {
        if (map[call.id]) {
          callIds.push(map[call.id])
          delete map[call.id]
        }
      })
    }
    return callIds
  }

  /**
   * Handler for initMedia's failure scenario.
   * Generates an error object based on the error code.
   * @method processMediaError
   * @param  {number} errorCode [description]
   * @return {Object} Error object.
   */
  function processMediaError (errorCode) {
    const mediaErrors = fcs.call.MediaErrors
    // TODO: Don't hardcode these here.
    const pluginUrls = {
      urlWin32bit: 'https://kandy-portal.s3.amazonaws.com/public/plugin/3.1.524/Kandy_Plugin_3.1.524.exe',
      urlWin64bit: 'https://kandy-portal.s3.amazonaws.com/public/plugin/3.1.524/Kandy_Plugin_3.1.524_x86_64.exe',
      urlMacUnix: 'https://kandy-portal.s3.amazonaws.com/public/plugin/3.1.524/Kandy_Plugin_3.1.524.pkg'
    }

    var error
    switch (errorCode) {
      case mediaErrors.WRONG_VERSION:
        error = {
          code: mediaErrors.WRONG_VERSION,
          message: 'Media Plugin Version Not Supported'
        }
        break
      case mediaErrors.NEW_VERSION_WARNING:
        error = {
          code: mediaErrors.NEW_VERSION_WARNING,
          message: 'New Plugin Version is available,',
          pluginUrls
        }
        break
      case mediaErrors.NOT_INITIALIZED:
        error = {
          code: mediaErrors.NOT_INITIALIZED,
          message: "Media couldn't be initialized."
        }
        break
      case mediaErrors.NOT_FOUND:
        error = {
          code: mediaErrors.NOT_FOUND,
          message: "Plugin couldn't be found!",
          pluginUrls
        }
        break
      case mediaErrors.NO_SCREENSHARING_WARNING:
        error = {
          code: mediaErrors.NO_SCREENSHARING_WARNING,
          message: 'ScreenShare extension could not be found.'
        }
    }

    // TODO: Provide a better error object. See PR #692 comments.
    return error
  }

  /**
   * Attempts to initialize media.
   * @method initMedia
   * @param {Object} options FCS options for initMedia.
   * @return {Promise}
   */
  function initializeMedia (options) {
    return new Promise(function (resolve) {
      function initMediaSuccess () {
        log.info('Media initialized successfully.')
        resolve({
          code: 0,
          message: 'Media initialized.',
          error: false
        })
      }
      function initMediaFailure (errorCode) {
        var result = processMediaError(errorCode)
        result.error = true
        resolve(result)
      }

      fcs.call.initMedia(initMediaSuccess, initMediaFailure, options)
    })
  }

  /**
   * Helper function for retrieving an ongoing call.
   * @method getInternalCall
   */
  function getInternalCall (callId) {
    var call = ongoingCalls[callId]
    if (!call) {
      log.info('Specified call Id does not match an on-going call.', callId)
      return false
    } else {
      log.debug('Internal call retrieved.', call)
      return call
    }
  }

  /**
   * Helper function for retrieving current audio bridges.
   * @method getInternalBridge
   */
  function getInternalBridge (bridgeId) {
    var bridge = audioBridges[bridgeId]
    if (!bridge) {
      log.info('Specified bridge Id does not match any current bridge.', bridgeId)
      return false
    } else {
      log.debug('Internal bridge retrieved.', bridge)
      return bridge
    }
  }

  /**
   * Handles a call state change.
   * Curried function so that it has the callId.
   * @method onStateChange
   * @param  {string} callId - ID of the call that changed state.
   * @return {Function} - Function that gets attached to fcsCall.onStateChange function.
   */
  function onStateChange (callId) {
    // @param {number} state - New state of the call.
    // @param {string} statusCode - Code representing the reason for the state change.
    // @param {string} reasonText - Explanation of the status code.
    // @param {Object} localStatusAndReason - Context from local side. Uses different codes/texts.
    // @param {string} localStatusAndReason.localStatusCode - Local equivalent to statusCode.
    // @param {string} localStatusAndReason.localReasonText - Local equivalent to reasonText.
    return function (state, statusCode, reasonText, localStatusAndReason) {
      // Treat "renegotiating" state changes as "in call" state.
      if (state === fcs.call.States.RENEGOTIATION) {
        log.debug('Renegotiating call state change. Treating as "in call".', callId)
        state = fcs.call.States.IN_CALL
      }

      // Restructure FCS' state change context objects.
      // Only one side ever has useful information.
      var transition
      if (statusCode && reasonText) {
        transition = {
          code: statusCode,
          reasonText: reasonText
        }
      } else if (localStatusAndReason) {
        transition = {
          code: localStatusAndReason.localStatusCode,
          reasonText: localStatusAndReason.localReasonText
        }
      } else {
        transition = {}
      }

      // If the state change was an end, remove the call internally.
      //      FCS has already dereference the call.
      if (state === fcs.call.States.ENDED) {
        log.debug(`Call state is ended, removing internal call reference: ${callId}`)
        delete ongoingCalls[callId]
      } else {
        // Retrieve other call info that may have changed.
        var call = getInternalCall(callId)
        var callInfo = {
          remoteParticipant: call.remoteParticipant,
          isVideoNegotiationAvailable: call.isVideoNegotiationAvailable ? call.isVideoNegotiationAvailable() : false
        }

        // If the FCS call has updated participant info, update the
        //      redux call with it. Try to ensure they have domains.
        const domain = getUsername() ? '@' + getUsername().split('@')[1] : ''
        if (call.callerNumber) {
          callInfo.from = call.callerNumber
          if (callInfo.from.indexOf('@') === -1) {
            callInfo.from += domain
          }
        }
        if (call.calleeNumber) {
          callInfo.to = call.calleeNumber
          if (callInfo.to.indexOf('@') === -1) {
            callInfo.to += domain
          }
        }
        if (call.callerName) {
          callInfo.callerName = call.callerName
        }
        if (call.calleeName) {
          callInfo.calleeName = call.calleeName
        }

        // Don't try things on a joined call. They're "special".
        if (!call.getJoin()) {
          callInfo.remoteVideoState = call.getRemoteVideoState()
        }
      }
      log.debug(`Call state change. ID: ${callId}, state: ${FCS_CALL_STATES[state]}, context: ${transition}`)
      // Go to the redux level.
      api.onCallStateChange(callId, FCS_CALL_STATES[state], transition, callInfo)
    }
  }

  /**
   * Handles a call media state change.
   * Curried function so that it has the callId.
   * @method onStateChange
   * @param  {string} callId - ID of the call that changed state.
   * @return {Function} - Function that gets attached to fcsCall.onMediaStateChange function.
   */
  function onMediaStateChange (callId) {
    // @param {number} mediaState New media state of the call.
    return function (mediaState) {
      log.debug(`Call media state change. ID: ${callId}, state: ${FCS_ICE_MEDIA_STATES[mediaState]}.`)
      // Go to the redux level.
      api.onMediaStateChange(callId, FCS_ICE_MEDIA_STATES[mediaState])
    }
  }

  /*
   * Register a listener to FCS for incoming calls.
   * Process the call as needed by the shim,
   *      then go to the redux level.
   * @method onReceived
   * @param  {Object}   call - FCS call object.
   */
  fcs.call.onReceived = function (call) {
    // Create our own call ID for storing in state.
    const callId = uuidV4()
    ongoingCalls[callId] = call

    const callInfo = {
      from: call.callerNumber,
      callerName: call.callerName,
      to: getUsername(),
      remoteVideoState: call.getRemoteVideoState(),
      remoteParticipant: call.remoteParticipant,
      wrtcsSessionId: call.getId(),
      isVideoNegotiationAvailable: call.isVideoNegotiationAvailable ? call.isVideoNegotiationAvailable() : false
      // TODO: Get more info from the call.
      // TODO: Make call info consistent between
      //      making call and receiving call.
    }
    // Register for state changes.
    call.onStateChange = onStateChange(callId)
    call.onMediaStateChange = onMediaStateChange(callId)

    // Go to the redux level.
    api.onIncomingCall(callId, callInfo)
  }

  var api = {
    /**
     * Endpoint for registering a listener.
     * Equivalent to FCS' fcs.call.onReceived.
     * @method onIncomingCall
     * @param {Object} callInfo - Information about the incoming call.
     */
    onIncomingCall: null,
    /**
     * Endpoint for registering a listener
     * @method onBridgeCallsChanged
     * @param {Object} bridgeId - ID of the bridge whose call list has changed
     * * @param {Array} calls - list of call IDs
     */
    onBridgeCallsChanged: null,

    /**
     * Endpoint for registering a listener for call state changes.
     * Equivalent to FCS' call.onStateChange.
     * @method onCallStateChange
     * @param {string} callId - SDK call ID.
     * @param {string} state
     * @param {Object} transition
     */
    onCallStateChange: null,

    /**
     * Endpoint for registering a listener for call media state changes.
     * Equivalent to FCS' call.onMediaStateChange.
     * @method onCallMediaStateChange
     * @param {string} callId - SDK call ID.
     * @param {number} mediaState
     */
    onCallMediaStateChange: null,

    // Expose some fcs methods directly.
    setUserAuth: fcs.setUserAuth.bind(fcs),
    setTokenAuth: fcs.setTokenAuth.bind(fcs),
    setKandyUAT: fcs.setKandyUAT.bind(fcs),
    setRealm: fcs.setRealm.bind(fcs),
    initLogging: fcs.logManager.initLogging.bind(fcs),
    getNotificationCallbacks () {
      return fcs.NotificationCallbacks
    },
    getGlobalBroadcaster () {
      return fcs.GlobalBroadcaster
    },

    setConnectionInfo (connectionInfo) {
      fcs.setConnectionInfo(connectionInfo)
    },

    setup (fcsConfig) {
      // true = delayInitMedia, because FCS doesn't provide an initMedia
      // response when done on setup, but all subsequent initMedias will
      // be returned as a success.
      fcs.setup(fcsConfig, true)
    },

    /**
     * Starts a call.
     * Equivalent to initializing media then starting a call.
     *
     * @method makeCall
     * @param  {string} callId - call Id
     * @param  {Object} options - Call options.
     * @return {Promise}
     */
    makeCall (callId, options) {
      return new Promise(function (resolve, reject) {
        /**
         * Callback provided to FCS' startCall.
         * @method startCallSuccess
         */
        function startCallSuccess (fcsCall) {
          log.debug('Successful call start.', fcsCall)

          // Save an internal reference to the call.
          ongoingCalls[callId] = fcsCall

          // Register for state changes.
          fcsCall.onStateChange = onStateChange(callId)
          fcsCall.onMediaStateChange = onMediaStateChange(callId)
          options.wrtcsSessionId = fcsCall.getId()
          options.isVideoNegotiationAvailable = fcsCall.isVideoNegotiationAvailable
            ? fcsCall.isVideoNegotiationAvailable()
            : false

          // Go back to the redux level.
          resolve({ callId, options })
        }

        /**
         * Callback provided to FCS' startCall.
         * @method startCallFailure
         */
        function startCallFailure (errorCode) {
          log.debug('Failed to start call.', errorCode)
          const message = errorCode === 38 ? 'No media specified for call.' : 'Failed to initialize call.'
          const error = {
            code: errorCode,
            message: message,
            type: 'call'
          }
          // Go back to the redux level.
          reject({ error, options })
        }

        /**
         * Callback provided to FCS' initMedia.
         * @method mediaSuccess
         */
        function mediaSuccess () {
          log.info('Call media initialized successfully.')
          log.debug('Starting call with FCS options', options)

          fcs.call.startCall(
            options.from,
            options.contact,
            options.to,
            startCallSuccess,
            startCallFailure,
            options.isVideoEnabled,
            options.sendInitialVideo,
            options.videoResolution,
            {
              isAudioEnabled: options.isAudioEnabled,
              sendScreenShare: options.sendScreenShare,
              mediaSourceId: options.mediaSourceId,
              customParameters: options.customParameters
            }
          )
        }

        /**
         * Callback provided to FCS' initMedia.
         * @method initMediaFailure
         */
        function initMediaFailure (errorCode) {
          const error = processMediaError(errorCode)
          if (error.code === fcs.call.MediaErrors.NO_SCREENSHARING_WARNING) {
            // This is not a failure case, just a warning.
            log.warn('Media initialized, but could not detect extension for screensharing.')
            mediaSuccess()
          } else {
            log.debug('Call media failed to initialize. Check media support.', error)
            error.type = 'media'
            reject({ error, options })
          }
        }

        // Options specifically for fcs.call.initMedia.
        const mediaOptions = {
          // TODO: Let default options be passed-in to plugin.
          remoteVideoContainer: options.remoteVideoContainer,
          localVideoContainer: options.localVideoContainer
        }

        log.debug('Initializing call media.', mediaOptions)
        // Start by calling FCS' initMedia.
        // Then go into the shim's success / failure callback.
        // Then we go to the middleware's success / failure callback.
        // TODO: Don't call this directly here; use the api.initMedia function.
        //      Change it when the middleware is refactored into sagas.
        fcs.call.initMedia(mediaSuccess, initMediaFailure, mediaOptions)
      })
    },

    /**
     * Answers an incoming call.
     * @method answerCall
     * @param  {string}   callId  Id of the call to answer.
     * @param  {Object}   options Call options.
     * @return {Promise}
     */
    answerCall (callId, options) {
      return new Promise(function (resolve, reject) {
        var call = ongoingCalls[callId]
        if (!call) {
          log.info('Specified call Id does not match an on-going call.', callId)
          reject({
            type: 'call',
            callId,
            error: 'Invalid call id.'
          })
        }

        // Callback provided to FCS' call.answer.
        function answerSuccess () {
          log.debug('Successful answer call.', call)
          resolve({ callId })
        }

        // Callback provided to FCS' call.answer.
        function answerFailure (errorCode) {
          log.debug('Failed to answer call.', errorCode)
          const error = {
            code: errorCode,
            message: 'Failed to answer call.',
            type: 'call'
          }
          // Go back to the redux level.
          reject({ error, callId })
        }

        // Callback provided to FCS' initMedia
        function mediaSuccess () {
          log.info('Call media initialized successfully.')
          log.debug('Answering call with FCS options', {
            isVideoEnabled: options.isVideoEnabled,
            isAudioEnabled: options.isAudioEnabled,
            videoResolution: options.videoResolution,
            sendInitialVideo: options.sendInitialVideo
          })

          call.answer(answerSuccess, answerFailure, options.sendInitialVideo, options.videoResolution, {
            isAudioEnabled: options.isAudioEnabled,
            isVideoEnabled: options.isVideoEnabled,
            customParameters: options.customParameters
          })
        }

        // Callback provided to FCS' initMedia
        function mediaFailure (errorCode) {
          const error = processMediaError(errorCode)
          if (error.code === fcs.call.MediaErrors.NO_SCREENSHARING_WARNING) {
            // This is not a failure case, just a warning.
            log.warn('Media initialized, but could not detect extension for screensharing.')
            mediaSuccess()
          } else {
            log.debug('Call media failed to initialize. Check media support.', error)
            error.type = 'media'
            reject({ error, options })
          }
        }

        // Options specifically for fcs.call.initMedia.
        const mediaOptions = {
          // TODO: Let default options be passed-in to plugin/shim.
          remoteVideoContainer: options.remoteVideoContainer,
          localVideoContainer: options.localVideoContainer
        }

        log.debug('Initializing call media.', mediaOptions)
        // Start by calling FCS' initMedia.
        // Then go into the shim's success / failure callback.
        // Then we go to the middleware's success / failure callback.
        // TODO: Don't call this directly here; use the api.initMedia function.
        //      Change it when the middleware is refactored into sagas.
        fcs.call.initMedia(mediaSuccess, mediaFailure, mediaOptions)
      })
    },

    /**
     * Ignore an incoming call.
     * @method ignoreCall
     * @param  {string}   callId FCS id of the call to ignore.
     * @return {Promise}
     */
    ignoreCall (callId) {
      return new Promise(function (resolve, reject) {
        function ignoreSuccess () {
          log.info('Successfully ignored call.')
          // Go back to the redux level.
          resolve({ callId })
        }

        function ignoreFailure (error) {
          log.info('Failed to ignore call.')
          // Go back to the redux level.
          reject({ error, callId })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.ignore(ignoreSuccess, ignoreFailure)
        } else {
          reject({ callId, error: 'Call not found. ' })
        }
      })
    },

    /**
     * Reject an incoming call.
     * @method rejectCall
     * @param  {string}   callId FCS id of the call to reject.
     * @return {Promise}
     */
    rejectCall (callId) {
      return new Promise(function (resolve, reject) {
        function rejectSuccess () {
          log.info('Successfully rejected call.')
          // Go back to the redux level.
          resolve({ callId })
        }

        function rejectFailure (error) {
          log.info('Failed to reject call.')
          // Go back to the redux level.
          reject({ error, callId })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.reject(rejectSuccess, rejectFailure)
        } else {
          reject({ callId, error: 'Call not found. ' })
        }
      })
    },

    /**
     * Ends a call.
     * @method endCall
     * @param  {string} callId Id of the call to end.
     * @return {Promise}
     */
    endCall (callId) {
      return new Promise(function (resolve, reject) {
        function endSuccess () {
          log.info('Successfully ended call.')
          // Go back to the redux level.
          resolve({ callId })
        }
        function endFailure (error) {
          log.info('Failed to end call.')
          // Go back to the redux level.
          reject({ error, callId })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.end(endSuccess, endFailure)
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /** Mutes a call.
     * @method muteCall
     * @param  {string} callId The ID of the call being acted on.
     * @return {Promise}
     */
    muteCall (callId) {
      return new Promise(function (resolve, reject) {
        var call = getInternalCall(callId)
        if (call) {
          // FCS' mute function does not take callbacks.
          call.mute()
          log.info('Call muted.')
          resolve({ callId })
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Unmutes a call.
     * @method unMuteCall
     * @param  {string} callId The ID of the call being acted on.
     * @return {Promise}
     */
    unMuteCall (callId) {
      return new Promise(function (resolve, reject) {
        var call = getInternalCall(callId)
        if (call) {
          // FCS' unmute function does not take callbacks.
          call.unmute()
          log.info('Call unmuted.')
          resolve({ callId })
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Silences a call.
     * @method silenceCall
     * @param  {string} callId The ID of the call being acted on.
     * @return {Promise}
     */
    silenceCall (callId) {
      return new Promise(function (resolve, reject) {
        var call = getInternalCall(callId)
        if (call) {
          // FCS' silence function does not take callbacks.
          call.silence()
          log.info('Call silenced.')
          resolve({ callId })
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Unsilences a call.
     * @method unSilenceCall
     * @param  {string} callId The ID of the call being acted on.
     * @return {Promise}
     */
    unSilenceCall (callId) {
      return new Promise(function (resolve, reject) {
        var call = getInternalCall(callId)
        if (call) {
          // FCS' unsilence function does not take callbacks.
          call.unsilence()
          log.info('Call unsilenced.')
          resolve({ callId })
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Gets custom parameters of call.
     * @method getCustomParameters
     * @param  {string} callId The ID of the call being acted on.
     * @return {Array.<{name: string, value:string}>} Custom parameters of the call.
     */
    getCustomParameters (callId) {
      const call = getInternalCall(callId)
      if (call) {
        return call.getCustomParameters()
      }
    },

    /**
     * Sets custom parameters of call.
     * @method setCustomParameters
     * @param  {string} callId The ID of the call being acted on.
     * @param  {Array.<{name: string, value:string}>} customParameters Custom parameters for the call.
     */
    setCustomParameters (callId, customParameters) {
      const call = getInternalCall(callId)
      if (call) {
        call.setCustomParameters(customParameters)
      }
    },

    /**
     * Starts video for a call.
     * @method startVideo
     * @param  {string} callId The ID of the call being acted on.
     * @param  {Object} params
     * @param  {Object} params.videoResolution
     *  @return {Promise}
     */
    startVideo (callId, params) {
      return new Promise(function (resolve, reject) {
        function startVideoSuccess () {
          log.info('Call video started successfully.')
          // Go back to the redux level.
          resolve({ callId, params })
        }
        function startVideoFailure (error) {
          log.info('Failed to start call video.')
          // Go back to the redux level.
          reject({ callId, params, error })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.videoStart(startVideoSuccess, startVideoFailure, params.videoResolution)
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Stops video for a call.
     * @method stopVideo
     * @param  {string} callId The ID of the call being acted on.
     * @return {Promise}
     */
    stopVideo (callId) {
      return new Promise(function (resolve, reject) {
        function stopVideoSuccess () {
          log.info('Call video stopped successfully.')
          // Go back to the redux level.
          resolve({ callId })
        }
        function stopVideoFailure (error) {
          log.info('Failed to stop call video.')
          // Go back to the redux level.
          reject({ callId, error })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.videoStop(stopVideoSuccess, stopVideoFailure)
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Holds a call.
     * @method holdCall
     * @param  {string} callId The ID of the call being acted on.
     * @return {Promise}
     */
    holdCall (callId) {
      return new Promise(function (resolve, reject) {
        function holdSuccess () {
          log.info('Successfully held call.')
          // Go back to the redux level.
          resolve({ callId })
        }
        function holdFailure (error) {
          log.info('Failed to hold call.')
          // Go back to the redux level.
          reject({ error, callId })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.hold(holdSuccess, holdFailure)
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * UnHolds a call.
     * @method unHoldCall
     * @param  {string} callId The ID of the call being acted on.
     * @return {Promise}
     */
    unHoldCall (callId) {
      return new Promise(function (resolve, reject) {
        function unHoldSuccess () {
          log.info('Successfully unheld call.')
          // Go back to the redux level.
          resolve({ callId })
        }
        function unHoldFailure (error) {
          log.info('Failed to unhold call.')
          // Go back to the redux level.
          reject({ error, callId })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.unhold(unHoldSuccess, unHoldFailure)
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Starts screensharing.
     * @param  {string} callId Id of the call being acted on.
     * @param  {Object} params
     * @param  {string} params.mediaSourceId Id of the media screen to share.
     * @param  {number} [params.width=1024] The width of the screen to request.
     * @param  {number} [params.height=768] The height of the screen to request.
     * @param  {number} [params.frameRate=15] The number of frames per second to request.
     * @param  {Function} screensharingStopped Callback function for when media is stopped manually.
     */
    startScreenshare (callId, params, screensharingStopped) {
      return new Promise(function (resolve, reject) {
        function startScreenshareSuccess () {
          log.info('Successfully shared screen.')
          // Go back to the redux level.
          resolve({ callId })
        }
        function startScreenshareFailure (error) {
          log.info('Failed to share screen.')
          // Go back to the redux level.
          reject({ callId, params, error })
        }

        // The mediaSourceId property is required.
        if (!params.hasOwnProperty('mediaSourceId')) {
          reject({ callId, error: 'No media source ID provided.' })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.screenSharingStart(startScreenshareSuccess, startScreenshareFailure, screensharingStopped, params)
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Stops screensharing.
     * @param  {string} callId Id of the call being acted on.
     */
    stopScreenshare (callId) {
      return new Promise(function (resolve, reject) {
        function stopScreenshareSuccess () {
          log.info('Successfully stopped screenshare.')
          // Go back to the redux level.
          resolve({ callId })
        }
        function stopScreenshareFailure (error) {
          log.info('Failed to stop screenshare.')
          // Go back to the redux level.
          reject({ callId, error })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.screenSharingStop(stopScreenshareSuccess, stopScreenshareFailure)
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Sends a DTMF tone over a call.
     * @method sendDTMF
     * @param  {string} callId The ID of the call being acted on.
     * @param {string} tone DTMF tone to send.
     * @return {Promise}
     */
    sendDTMF (callId, tone) {
      return new Promise(function (resolve, reject) {
        var call = getInternalCall(callId)
        if (call) {
          // FCS' sendDTMF does not take callbacks.
          call.sendDTMF(tone)
          resolve()
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Explicitly sends custom parameters of a call.
     * @method sendCustomParameters
     * @param  {string} callId The ID of the call being acted on.
     * @return {Promise}
     */
    sendCustomParameters (callId) {
      return new Promise(function (resolve, reject) {
        var call = getInternalCall(callId)

        function sendCustomParametersSuccess () {
          log.info('Successfully sent custom parameters.')
          // Go back to the redux level.
          resolve({ callId })
        }
        function sendCustomParametersFailure (error) {
          log.info('Failed to send custom parameters.')
          // Go back to the redux level.
          reject({ callId, error })
        }

        if (call) {
          // FCS' sendCustomParameters.
          call.sendCustomParameters(sendCustomParametersSuccess, sendCustomParametersFailure)
        } else {
          reject({ callId, error: 'Call not found.' })
        }
      })
    },

    /**
     * Forwards a call to another user.
     * @method forwardCall
     * @param  {string} callId The ID of the call being acted on.
     * @param  {string} destination Full user ID of another user.
     * @return {Promise}
     */
    forwardCall (callId, destination) {
      return new Promise(function (resolve, reject) {
        function forwardSuccess () {
          log.info('Successfully forwarded call.')
          // Go back to the redux level.
          resolve({ callId })
        }
        function forwardFailure (errorCode) {
          log.info('Failed to forward call.')
          // Go back to the redux level.
          const error = {
            code: errorCode,
            message: 'Failed to forward call.'
          }
          reject({ callId, error })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.forward(destination, forwardSuccess, forwardFailure)
        } else {
          reject({ callId, error: { message: 'Failed to forward call.' } })
        }
      })
    },

    /**
     * Transfers a call to another user.
     * @method directTransfer
     * @param  {string} callId The ID of the call being acted on.
     * @param  {string} destination Full user ID of another user.
     * @return {Promise}
     */
    directTransfer (callId, destination) {
      return new Promise(function (resolve, reject) {
        function transferSuccess () {
          log.info('Successfully transferred call.')
          // Go back to the redux level.
          resolve({ callId })
        }
        function transferFailure (errorCode) {
          log.info('Failed to transfer call.')
          // Go back to the redux level.
          const error = {
            code: errorCode,
            message: 'Failed to transfer call.'
          }
          reject({ callId, error })
        }

        var call = getInternalCall(callId)
        if (call) {
          call.directTransfer(destination, transferSuccess, transferFailure)
        } else {
          reject({ callId, error: { message: 'Failed to transfer call.' } })
        }
      })
    },

    /**
     * Transfers a call to another call.
     * @method consultativeTransfer
     * @param  {string} callId The ID of the call being acted on.
     * @param  {string} destination The callId of another call.
     * @return {Promise}
     */
    consultativeTransfer (callId, destination) {
      return new Promise(function (resolve, reject) {
        function transferSuccess () {
          log.info('Successfully transferred call.')
          // Go back to the redux level.
          resolve({ callId })
        }
        function transferFailure (errorCode) {
          log.info('Failed to transfer call.')
          // Go back to the redux level.
          const error = {
            code: errorCode,
            message: 'Failed to transfer call.'
          }
          reject({ callId, error })
        }

        var call = getInternalCall(callId)
        var dest = getInternalCall(destination)
        if (call && dest) {
          call.consultativeTransfer(dest.getId(), transferSuccess, transferFailure)
        } else if (!call) {
          reject({ callId, error: { message: 'Invalid call id.' } })
        } else {
          reject({ callId, error: { message: 'Invalid destination.' } })
        }
      })
    },

    /**
     * Joins a call with another call.
     * @method joinCall
     * @param  {string} callId The ID of the call being acted on.
     * @param  {string} destination The callId of another call.
     * @param  {Object} options - Media options.
     * @return {Promise}
     */
    joinCall (callId, destination, options) {
      return new Promise(function (resolve, reject) {
        function joinSuccess (joinedCall) {
          log.info('Successfully joined call.')
          // Create our own call ID for storing in state.
          const joinedCallId = uuidV4()
          ongoingCalls[joinedCallId] = joinedCall

          // Register for state changes.
          joinedCall.onStateChange = onStateChange(joinedCallId)
          joinedCall.onMediaStateChange = onMediaStateChange(joinedCallId)

          // Go back to the redux level.
          resolve({
            callId: joinedCallId,
            joinedCalls: [callId, destination],
            callState: FCS_CALL_STATES[joinedCall.callState]
          })
        }
        function joinFailure (errorCode) {
          log.info('Failed to join call.')
          // Go back to the redux level.
          const error = {
            code: errorCode,
            message: 'Failed to join call.'
          }
          reject({
            callId,
            joinedCalls: [callId, destination],
            error
          })
        }

        initializeMedia(options).then(function (result) {
          if (result.code === 0 || result.code === fcs.call.MediaErrors.NO_SCREENSHARING_WARNING) {
            // Media initialized, perform the join operation.
            var call = getInternalCall(callId)
            var dest = getInternalCall(destination)
            if (call && dest) {
              const withVideo = call.localVideo || dest.localVideo
              call.join(dest, joinSuccess, joinFailure, withVideo)
            } else if (!call) {
              reject({ callId, error: { message: 'Invalid call id.' } })
            } else {
              reject({
                callId: destination,
                error: { message: 'Invalid destination.' }
              })
            }
          } else {
            // Failed to initialize media; return an error.
            reject({
              callId,
              error: {
                message: `Failed to initialize call media: ${result.message}`,
                type: 'media',
                code: result.code
              }
            })
          }
        })
      })
    },

    /**
     * Sets the default devices for FCS to use for new calls.
     * @method setDefaultDevices
     * @param  {Object} devices
     * @return {Promise}
     */
    setDefaultDevices (devices) {
      return new Promise(function (resolve, reject) {
        function onSpeakerSuccess () {
          if (devices.speaker) {
            log.info(`Set selected speaker as default: ${devices.speaker}.`)
          }
          if (devices.camera) {
            fcs.call.setSelectedCameraId(devices.camera)
            log.info(`Set selected camera as default: ${devices.camera}.`)
          }
          if (devices.microphone) {
            fcs.call.setSelectedMicrophoneId(devices.microphone)
            log.info(`Set selected microphone as default: ${devices.microphone}.`)
          }
          resolve(devices)
        }
        function onSpeakerFailure (error) {
          log.info('Failed to set default speaker.', error)
          reject({
            device: 'speaker',
            deviceId: devices.speaker,
            error
          })
        }

        // Only setSelectedSpeakerId has callbacks, so do that one first.
        if (devices.speaker) {
          fcs.call.setSelectedSpeakerId(devices.speaker, onSpeakerSuccess, onSpeakerFailure)
        } else {
          // If we aren't setting the speaker, go straight to the success callback.
          onSpeakerSuccess()
        }
      })
    },

    /**
     * Prompts for permission to use media.
     * @method promptUserMedia
     * @param  {Object} params
     * @param  {boolean} params.video
     * @param  {boolean} params.audio
     *  @return {Promise}
     */
    promptUserMedia (params) {
      return new Promise(function (resolve, reject) {
        function promptUserMediaSuccess () {
          // Go back to the redux level.
          log.info('Media permissions granted.')
          resolve({ params })
        }

        function promptUserMediaFailure (error) {
          // Go back to the redux level.
          log.info('Media permissions not granted.', error)

          // Convert the callShim error object into a BasicError.
          const basicError = new BasicError({
            message: 'Media permissions not granted.',
            code: error
          })

          reject({ params, basicError })
        }
        fcs.call.getUserMedia(promptUserMediaSuccess, promptUserMediaFailure, params)
      })
    },

    /**
     * Changes speaker for on-going calls.
     * @method changeSpeaker
     * @param  {string} speakerId
     * @return {Promise}
     */
    changeSpeaker (speakerId) {
      return new Promise(function (resolve, reject) {
        function changeSpeakerSuccess () {
          log.info('Successfully changed speaker.')
          resolve(speakerId)
        }

        function changeSpeakerFailure (error) {
          log.info('Failed to change speaker.', error)
          reject({ speakerId, error })
        }

        if (speakerId) {
          fcs.call.changeSpeaker(speakerId, changeSpeakerSuccess, changeSpeakerFailure)
        } else {
          reject({ error: 'Invalid speaker id provided.' })
        }
      })
    },

    /**
     * Changes the camera/microphone used for the specified call.
     * @method changeInputDevices
     * @param  {string} callId
     * @return {Promise}
     */
    changeInputDevices (callId) {
      return new Promise(function (resolve, reject) {
        function changeDevicesSuccess () {
          resolve(callId)
        }
        function changeDevicesFailure (fcsError) {
          reject({ callId, error: fcsError })
        }

        const call = getInternalCall(callId)
        if (call) {
          /*
           * FCS' API says the first parameter should be { call }, but internally
           *    FCS checks for `params.callid` as if the first parameter is just
           *    `call`. Change how the shim behaves to "fix" this, instead of
           *    changing FCS, to prevent any other issues.
           */
          fcs.call.changeDevices({ callid: call.getId() }, changeDevicesSuccess, changeDevicesFailure)
        } else {
          reject({ callId, error: 'Call not found. ' })
        }
      })
    },

    /**
     * Starts the local video stream and displays it to the user.
     * @method startLocalVideo
     * @param  {HTMLElement} container The container to use for local video.
     * @return {Promise}
     */
    startLocalVideo (container) {
      return new Promise(function (resolve, reject) {
        function getUserMediaSuccess (streamInfo) {
          // Render the local video into the container.
          fcs.call.createStreamRenderer(streamInfo.stream, container, {
            audio: false
          })
          // Store the local stream's info.
          localStream = {
            stream: streamInfo,
            container: container
          }
          log.info('Started local video stream.')
          resolve(container)
        }
        function getUserMediaFailure () {
          log.info('Failed to start local video.')
          const error = {
            message: 'Could not start local video.',
            type: 'video'
          }
          reject({ error })
        }

        if (localStream) {
          log.info('Local video is already being displayed.')
          const error = {
            message: 'Local video is already being displayed. Stop local video before re-starting.',
            type: 'generic'
          }
          reject({ error })
          return
        } else if (!container) {
          const error = {
            message: 'No video container provided; cannot start local video.',
            type: 'generic'
          }
          reject({ error })
          return
        }

        // Re-use the callShim's initMedia functionality.
        initializeMedia().then(function (result) {
          if (result.code === 0 || result.code === fcs.call.MediaErrors.NO_SCREENSHARING_WARNING) {
            // Get the local user video media.
            fcs.call.getUserMedia(getUserMediaSuccess, getUserMediaFailure, {
              audio: false,
              video: true
            })
          } else {
            // initMedia error.
            const error = {
              message: 'Could not initialize media. Please check initMedia.',
              type: 'media'
            }
            reject({ error })
          }
        })
      })
    },

    /**
     * Stops the local video stream created in `startLocalVideo`.
     * @method stopLocalVideo
     * @return {Promise}
     */
    stopLocalVideo () {
      return new Promise(function (resolve, reject) {
        // Only act on the stream that was started earlier via `startLocalVideo`.
        if (localStream) {
          fcs.call.disposeStreamRenderer(localStream.container)
          fcs.call.removeStreamById(localStream.stream.id)
          // Remove the callShim's 'flag' for local video.
          localStream = null
          log.info('Local video stream stopped.')
          resolve()
        } else {
          log.info('No local video stream to stop.')
          reject({
            message: 'No on-going local stream to stop.'
          })
        }
      })
    },

    /**
     * Attempts to initialize media.
     * @method initMedia
     * @param {Object} options FCS options for initMedia.
     * @return {Promise}
     */
    initMedia: initializeMedia,

    /**
     * Creates a local bridge for audio calls.
     * @method createLocalAudioBridge
     * @param  {string} bridgeId
     * @param  {Object} options
     * @return {Promise}
     */
    createLocalAudioBridge (bridgeId) {
      return new Promise(function (resolve, reject) {
        function createBridgeSuccess (bridge) {
          log.debug('Successfully created bridge.', bridgeId)

          /*
           * Callback method allows FCS to send updated call lists
           * after calls are added or removed, providing us with the opportunity
           * to ensure the bridge's state is up to date in the SDK
           *
           * @param {Array} calls
           */
          bridge.onCallsChange = function (fcsCalls) {
            const calls = getUnifiedSdkCalls(fcsCalls)
            api.onBridgeCallsChanged(bridgeId, calls)
          }

          // Save an internal reference to the bridge.
          audioBridges[bridgeId] = bridge
          resolve()
        }

        function createBridgeFailure () {
          log.debug('Failed to create bridge.', bridgeId)
          // Error is either that the FCS media isn't set up (ie. user
          //      not logged in) or feature not supported in browser.
          const error = new BasicError({
            message: 'Feature not supported by browser.',
            code: bridgeCodes.NOT_SUPPORTED
          })
          reject(error)
        }

        fcs.call.createLocalAudioBridge(createBridgeSuccess, createBridgeFailure)
      })
    },

    /**
     * Closes an existing audio bridge.
     * @method closeAudioBridge
     * @param  {string} bridgeId
     * @return {Promise}
     */
    closeAudioBridge (bridgeId) {
      return new Promise(function (resolve, reject) {
        const bridge = getInternalBridge(bridgeId)
        if (bridge) {
          bridge.close()
          resolve()
        } else {
          const error = new BasicError({
            message: 'Provided ID does not match an existing bridge.',
            code: bridgeCodes.INVALID_INPUT
          })
          reject(error)
        }
      })
    },

    addCallToBridge (bridgeId, callId) {
      return new Promise(function (resolve, reject) {
        function addCallSuccess () {
          log.debug('Successfully added call to bridge.', bridgeId)
          resolve()
        }

        function addCallFailure (errCode) {
          log.debug('Failed to add call to bridge.', errCode)
          let error
          if (errCode === 29) {
            // FCS' "entry already exists" code.
            error = new BasicError({
              message: 'Call already added to bridge.',
              code: bridgeCodes.ALREADY_EXISTS
            })
          } else if (errCode === 24) {
            // FCS' "media not found" code.
            error = new BasicError({
              message: 'Failed to find media.',
              code: bridgeCodes.MEDIA_NOT_FOUND
            })
          } else if (errCode === 3) {
            // FCS' "state" code.
            error = new BasicError({
              message: 'Invalid state.',
              code: bridgeCodes.INVALID_STATE
            })
          } else {
            error = new BasicError({
              message: 'An unknown error occurred.',
              code: bridgeCodes.UNKNOWN_ERROR
            })
          }
          reject(error)
        }

        const bridge = getInternalBridge(bridgeId)
        const call = getInternalCall(callId)

        if (bridge && call) {
          bridge.add(call, addCallSuccess, addCallFailure)
        } else {
          const error = new BasicError({
            message: 'A provided ID did not match an existing bridge and/or call.',
            code: bridgeCodes.INVALID_INPUT
          })
          reject(error)
        }
      })
    },

    /**
     * Removes a call from the specified audio bridge.
     * @method removeCallFromBridge
     */
    removeCallFromBridge (bridgeId, callId) {
      return new Promise(function (resolve, reject) {
        function removeCallSuccess () {
          log.debug('Successfully removed call from bridge.', bridgeId)
          resolve()
        }
        function removeCallFailure (err) {
          log.debug('Failed to remove call from bridge.', err)
          let error
          if (err === 22) {
            // FCS' "invalid parameter" code.
            error = new BasicError({
              message: 'Specified call not in bridge.',
              code: bridgeCodes.NOT_FOUND
            })
          } else {
            error = new BasicError({
              message: 'An unknown error occurred.',
              code: bridgeCodes.UNKNOWN_ERROR
            })
          }
          reject(error)
        }

        const bridge = getInternalBridge(bridgeId)
        const call = getInternalCall(callId)

        if (bridge && call) {
          bridge.remove(call, removeCallSuccess, removeCallFailure)
        } else {
          const error = new BasicError({
            message: 'A provided ID did not match an existing bridge and/or call.',
            code: bridgeCodes.INVALID_INPUT
          })
          reject(error)
        }
      })
    },

    /**
     * Mutes local audio for all calls in the bridge.
     * @method muteAudioBridge
     */
    muteAudioBridge (bridgeId) {
      return new Promise(function (resolve, reject) {
        const bridge = getInternalBridge(bridgeId)
        if (bridge) {
          bridge.muteLocalAudio()
          resolve()
        } else {
          const error = new BasicError({
            message: 'Provided ID does not match an existing bridge.',
            code: bridgeCodes.INVALID_INPUT
          })
          reject(error)
        }
      })
    },

    /**
     * Unmutes local audio for all calls in the bridge.
     * @method unmuteAudioBridge
     */
    unmuteAudioBridge (bridgeId) {
      return new Promise(function (resolve, reject) {
        const bridge = getInternalBridge(bridgeId)
        if (bridge) {
          bridge.unmuteLocalAudio()
          resolve()
        } else {
          const error = new BasicError({
            message: 'Provided ID does not match an existing bridge.',
            code: bridgeCodes.INVALID_INPUT
          })
          reject(error)
        }
      })
    },

    /**
     * Silences remote audio for all calls in the bridge.
     * @method silenceAudioBridge
     */
    silenceAudioBridge (bridgeId) {
      return new Promise(function (resolve, reject) {
        const bridge = getInternalBridge(bridgeId)
        if (bridge) {
          bridge.silenceRemoteAudio()
          resolve()
        } else {
          const error = new BasicError({
            message: 'Provided ID does not match an existing bridge.',
            code: bridgeCodes.INVALID_INPUT
          })
          reject(error)
        }
      })
    },

    /**
     * Unsilences remote audio for all calls in the bridge.
     * @method unsilenceAudioBridge
     */
    unsilenceAudioBridge (bridgeId) {
      return new Promise(function (resolve, reject) {
        const bridge = getInternalBridge(bridgeId)
        if (bridge) {
          bridge.unsilenceRemoteAudio()
          resolve()
        } else {
          const error = new BasicError({
            message: 'Provided ID does not match an existing bridge.',
            code: bridgeCodes.INVALID_INPUT
          })
          reject(error)
        }
      })
    }
  }
  return api
}
