                oSdp.sdp = self.updateCodecs(call, oSdp.sdp);
                oSdp.sdp = _sdpParser.updateOpusConfig(oSdp.sdp, _config.opusConfig);
                oSdp.sdp = _sdpParser.setTrickleOption(oSdp.sdp, _config.trickleIceSupport !== CONSTANTS.TRICKLE.NONE);

