import * as actionTypes from '../actionTypes'
import { handleActions } from 'redux-actions'

const reducers = {}

// Replace old devices with newly checked devices.
reducers[actionTypes.CHECK_DEVICES_FINISH] = {
  next (state, action) {
    return {
      ...state,
      ...action.payload
    }
  }
}

reducers[actionTypes.SET_DEFAULT_DEVICES_FINISH] = {
  next (state, action) {
    // Replace the default devices with the newly set defaults.
    return {
      ...state,
      defaults: {
        ...state.defaults,
        ...action.payload
      }
    }
  }
}

const reducer = handleActions(reducers, {})
export default reducer
