import { eventChannel } from 'redux-saga'
import { call, put, take, takeEvery } from 'redux-saga/effects'
import { logManager } from '../../logs'
import BasicError, { callCodes } from '../../errors'

import { devicesActions } from '../interface/actions'
import * as actionTypes from '../interface/actionTypes'
import { convertDevices } from '../utils/mediaDevices'

// Get the logger
const log = logManager.getLogger('CALL')

/**
 * Naively checks whether navigator.mediaDevices is supported in this environment.
 * @method isSupported
 * @return {boolean}
 */
function isSupported () {
  return typeof navigator !== 'undefined' && typeof navigator.mediaDevices !== 'undefined'
}

// TODO: Somewhere else should "initialize" this.
if (!isSupported()) {
  log.warn('MediaDevices not supported; will not watch for media device changes.')
}

/**
 * Saga watcher.
 * Forwards actions to checkMediaDevices saga.
 * @method watchCheckMediaDevices
 */
export function * watchCheckMediaDevices () {
  yield takeEvery(actionTypes.CHECK_DEVICES, checkMediaDevices)
}

/**
 * Retrieves media devices when requested.
 * @method checkMediaDevices
 */
export function * checkMediaDevices () {
  if (!isSupported()) {
    yield put(
      devicesActions.checkDevicesFinish({
        error: new BasicError({
          code: callCodes.NOT_SUPPORTED,
          message: 'MediaDevices APIs are not supported in this environment.'
        })
      })
    )
    return
  }

  // Redux-saga doesn't like calling this directly.
  // TODO: Find out why / find a better way to do this.
  function getDevices () {
    return navigator.mediaDevices.enumerateDevices()
  }

  let devices
  try {
    devices = yield call(getDevices)
  } catch (error) {
    log.debug('Failed to retrieve media devices.', error)
    yield put(devicesActions.checkDevicesFinish({ error }))
    return
  }

  log.debug('Retrieved latest media devices.', devices)
  // Format the device list into a user-friendly object.
  devices = convertDevices(devices)
  yield put(devicesActions.checkDevicesFinish({ devices }))
}

/**
 * Listens for device changes.
 * Groups changes within a short time period into a single emitted event.
 * @method onDeviceChange
 */
export function * onDeviceChange () {
  if (!isSupported()) {
    return
  }

  const deviceChangeChannel = eventChannel(emit => {
    let recentDeviceChange = false

    navigator.mediaDevices.ondevicechange = () => {
      log.info('Media device change detected.')
      // A physical device change results in one event per
      // device "kind". Group the events together.
      if (!recentDeviceChange) {
        recentDeviceChange = true
        setTimeout(function () {
          recentDeviceChange = false
          emit(devicesActions.checkDevices())
        }, 50)
      }
    }

    // Return unsubscribe function.
    return () => {
      navigator.mediaDevices.ondevicechange = null
    }
  })
  while (true) {
    const action = yield take(deviceChangeChannel)
    yield put(action)
  }
}
