import {sdpParse, sdpWrite} from './parser';

/*
 * @method createSdpPipeline
 * @param {function[]} handlers An array of sdp handlers. These are functions that are given the sdp and a change to return a modified sdp.
 * @param {function} sdpLogger The logger for the sdp pipeline. This function is called like a handler, with the same params, but is guaranteed to be called at the end of the pipeline.
 * @returns {function} sdpPipeline The sdpPipeline that will call all of the handlers in order
 */
export function createSdpPipeline(getHandlers, sdpLogger) {
    /*
     * @method sdpPipeline
     * @param {Object} sdp The sdp to put through the pipeline
     * @param {string} operation The operation this sdp will be used for. Values can be found in js/webrtc/adaptor/constants.js
     * @param {string} step The step at which the pipeline is being called. Can be 'PRE_LOCAL', 'POST_LOCAL', 'PRE_REMOTE' and 'POST_REMOTE'.
     * @param {string} type The type of sdp. Can be 'offer' or 'answer'.
     * @returns {Object} sdp The possibly modified sdp.
     */
    return (callId, sdp, operation, step, type) => {
        const handlers = getHandlers();
        const originalSdp = Object.freeze(sdp);
        let newSdp = originalSdp;

        // Function that is provided to handlers that needs to be called
        // to enable the next handler to be called.
        let nextCalled = false;
        function next(sdp) {
            nextCalled = true;
            return sdp;
        }

        function callHandler(handler) {
            return handler({
                next,
                originalSdp,
                currentSdp: newSdp,
                operation,
                step,
                type,
                callId
            });
        }

        for (let i=0; i<handlers.length; i++) {
            // Rest the flag back for each handler
            nextCalled = false;

            newSdp = callHandler(handlers[i]);

            if (!newSdp) {
                throw new Error('An SDP handler failed to return an sdp');
            }

            // If the next function was not called, we exit the pipeline
            if (!nextCalled) {
                break;
            }
        }

        newSdp = callHandler(sdpLogger);

        return newSdp;
    };
}

// This is a utility function that wraps the sdp pipeline. The wrapper will
// convert an sdp string to provide an sdp object to the pipeline and it will
// convert the return value back to a string.
export function createConvertedSdpPipeline(getHandlers, sdpLogger) {
    const pipeline = createSdpPipeline(getHandlers, sdpLogger);

    return (callId, sdpString, operation, step, type) => {
        const sdpObject = sdpParse(sdpString);
        const newSdp = pipeline(callId, sdpObject, operation, step, type);

        // Optimization: no need to write the string if the sdp hasn't changed
        if (sdpObject === newSdp) {
            return sdpString;
        }

        return sdpWrite(newSdp);
    };
}
