import * as actionTypes from '../actionTypes'
import { combineActions, handleActions } from 'redux-actions'
import { concat } from 'lodash/fp'

// Top-tier reducers: Handles the whole audioBridge state.
const reducers = {}
// Bridge-tier reducers: Handles only a specific bridge's state.
const bridgeReducers = {}

reducers[actionTypes.CREATE_AUDIO_BRIDGE_FINISH] = {
  next (state, action) {
    const newBridge = {
      id: action.payload.bridgeId,
      options: action.payload.options,
      muted: false,
      silenced: false,
      calls: [],
      status: 'OPEN'
    }

    return concat(state, newBridge)
  }
}

/*
 * Bridge-tier reducers.
 * Receive a single bridge's state as state.
 */

bridgeReducers[actionTypes.CLOSE_AUDIO_BRIDGE_FINISH] = {
  next (state) {
    return {
      ...state,
      status: 'CLOSED'
    }
  }
}

bridgeReducers[actionTypes.MUTE_AUDIO_BRIDGE_FINISH] = {
  next (state) {
    return {
      ...state,
      muted: true
    }
  }
}

bridgeReducers[actionTypes.UNMUTE_AUDIO_BRIDGE_FINISH] = {
  next (state) {
    return {
      ...state,
      muted: false
    }
  }
}

bridgeReducers[actionTypes.SILENCE_AUDIO_BRIDGE_FINISH] = {
  next (state) {
    return {
      ...state,
      silenced: true
    }
  }
}

bridgeReducers[actionTypes.UNSILENCE_AUDIO_BRIDGE_FINISH] = {
  next (state) {
    return {
      ...state,
      silenced: false
    }
  }
}

bridgeReducers[actionTypes.UPDATE_AUDIO_BRIDGE_CALLS] = {
  next (state, action) {
    return {
      ...state,
      calls: action.payload.calls
    }
  }
}

/*
 * Combine all of the bridge-tier reducers into a single reducer,
 *      each with a default state of empty object.
 */
const bridgeReducer = handleActions(bridgeReducers, {})

// Actions routed to bridge-tier reducers.
const specificBridgeActions = combineActions(
  actionTypes.CLOSE_AUDIO_BRIDGE_FINISH,
  actionTypes.MUTE_AUDIO_BRIDGE_FINISH,
  actionTypes.UNMUTE_AUDIO_BRIDGE_FINISH,
  actionTypes.SILENCE_AUDIO_BRIDGE_FINISH,
  actionTypes.UNSILENCE_AUDIO_BRIDGE_FINISH,
  actionTypes.UPDATE_AUDIO_BRIDGE_CALLS
)

/*
 * Reducer to handle specific bridge actions.
 * Routes the actions to the bridge-tier reducers.
 */
reducers[specificBridgeActions] = (state, action) => {
  return state.map(function (bridge) {
    if (bridge.id === action.payload.bridgeId) {
      return bridgeReducer(bridge, action)
    } else {
      return bridge
    }
  })
}

/*
 * Combine all of the top-tier reducers into a single reducer,
 *      each with a default state of an empty object.
 */
const reducer = handleActions(reducers, [])
export default reducer
