import * as actionTypes from '../actionTypes'
import { handleActions } from 'redux-actions'

const reducers = {}

// Replace old media info with new info.
reducers[actionTypes.INIT_MEDIA_FINISH] = {
  next (state, action) {
    // If media state has been set, don't reset it. results from second+
    //      initMedia attempt may not be accurate.
    if (!state) {
      return action.payload
    } else {
      return state
    }
  }
}

const reducer = handleActions(reducers, '')
export default reducer
