import * as actionTypes from '../actionTypes'

/**
 * Helper function for formatting call actions.
 * @method callActionHelper
 * @param  {string} type - Action type.
 * @param  {string} callId - Id of the call being acted on.
 * @param  {Object} $2
 * @param  {Object} $2.callInfo - Call state properties.
 * @param  {Object} $2.error
 * @return {Object} A flux standard action.
 */
export function callActionHelper (type, callId, { callInfo, error, params } = {}) {
  const action = {
    type,
    payload: {}
  }

  if (error) {
    action.error = true
    const message = typeof error === 'string' ? error : error.message
    action.payload = new Error(message)
    // TODO: Ensure the middleware uses consistent error objects.
    // Ensure the action has the entire error object.
    action.payload.error = error
  }

  if (callInfo) {
    action.payload.callInfo = callInfo
  }
  if (params) {
    action.payload.params = params
  }
  action.payload.callId = callId

  return action
}

/**
 * Creates a make call action to start an outgoing call.
 * @method makeCall
 * @param  {string} callee       Full user ID of the call recipient.
 * @param  {string} callId       A unique identifier for the call.
 * @param  {Object} [options={}] Call options.
 * @returns {Object} A flux standard action representing the make call action.
 */
export function makeCall (callee, callId, options = {}) {
  const callInfo = {
    ...options,
    to: callee
  }
  return callActionHelper(actionTypes.MAKE_CALL, callId, {
    callInfo
  })
}

/**
 * Creates a make call finish action.
 * @method makeCallFinish
 * @param  {string} callId The outgoing call ID.
 * @param  {Object} callInfo Information about the call.
 * @param  {Object} error The error object, in the case of an error.
 * @returns {Object} A flux standard action representing the make call finish action.
 */
export function makeCallFinish (callId, callInfo, error) {
  return callActionHelper(actionTypes.MAKE_CALL_FINISH, callId, {
    callInfo,
    error
  })
}

