/**
 * Represents an action to send a DTMF tone over a call.
 * @method sendDTMF
 * @param  {string} callId The ID of the call being acted on.
 * @param {string} tone DTMF tone to send.
 * @returns {Object} A flux standard action.
 */
export function sendDTMF (callId, tone) {
  return callActionHelper(actionTypes.SEND_DTMF, callId, {
    params: { tone }
  })
}

/**
 * Represents the finish of an action to send a DTMF tone.
 * Error action only.
 * @method sendDTMFFinish
 * @param  {string} callId The ID of the call being acted on.
 * @param  {Object} error
 * @returns {Object} A flux standard action.
 */
export function sendDTMFFinish (callId, error) {
  var payload = new Error(error)
  payload.callId = callId

  return {
    type: actionTypes.SEND_DTMF_FINISH,
    error: true,
    payload
  }
}

