export function getProperty(obj, property) {
    return ((typeof obj[property]) === 'undefined') ? null : obj[property];
}

//WARNING: This function probably does not do what you think.
export function compose(base, extendme) {
    var prop;
    for (prop in base) {
        if (typeof base[prop] === 'function' && !extendme[prop]) {
            extendme[prop] = base[prop].bind(base);
        }
    }
}

export function getTimestamp() {
    return new Date().getTime();
}

export function parseAddress(address, contact) {
    var displayName = '';

    if (address.indexOf('sip:', 0) > -1) {
        address = address.replace('sip:', '');
    }

    if (contact === undefined || contact === null) {
        return (address.indexOf('@', 0) > -1) ? 'sip:' + address : address;
    }
    if (contact.firstName && contact.firstName !== '') {
        displayName += contact.firstName;
    }
    if (contact.lastName && contact.lastName !== '') {
        if (displayName === '') {
            displayName += contact.lastName;
        } else {
            displayName += ' ' + contact.lastName;
        }
    }
    if (displayName === '') {
        return (address.indexOf('@', 0) > -1) ? 'sip:' + address : address;
    }
    return displayName + '<' + ((address.indexOf('@', 0) > -1) ? 'sip:' + address : address) + '>';
}

export function getInteger(val) {
    if (isNaN(val) || val === null || val === undefined) {
        return 0;
    }
    return parseInt(val);
}

export function Queue() {
    var items;

    this.enqueue = function(item) {
        if (typeof(items) === 'undefined') {
            items = [];
        }
        items.push(item);
    };

    this.dequeue = function() {
        return items.shift();
    };

    this.peek = function() {
        return items[0];
    };

    this.size = function() {
        return typeof(items) === 'undefined' ? 0 : items.length;
    };
}

/*
 * Similar to http://api.jquery.com/jquery.param/
 */
export function serialize(object) {
    var encodedString = '',
        prop;
    for (prop in object) {
        if (object.hasOwnProperty(prop)) {
            if (encodedString.length > 0) {
                encodedString += '&';
            }
            encodedString += encodeURI(prop + '=' + object[prop]);
        }
    }
    return encodedString;
}

export function extend(target, object) {
    var prop;
    for (prop in object) {
        if (object.hasOwnProperty(prop)) {
            target[prop] = object[prop];
        }
    }
    return target;
}

/*
 * Always is a Promise.finally replacement helper. Will call the handler as part of the promise chain without
 * affecting the result of the promise.
 *
 * @param  {Promise} promise The promise to attach the finally handler to.
 * @param  {Function} handler A function which will be called when the passed in promise is settled (resolved or rejected)
 * @return {Promise} The new Promise with the handler attached.
 */
export function always(promise, handler) {
    return promise.then(
        // Success path, attach the always handler and then just a continuation of the param.
        param => new Promise(resolve => resolve(handler())).then(() => param),
        // Failure path, attach the always handler and then just a continuation with the re-thrown error.
        error => new Promise(resolve => resolve(handler())).then(() => { throw error; })
    );
}


export function Utils({ LogManager: _logManager }) {
    var logger;

    this.callFunctionIfExist = function callFunctionIfExist() {
        // Get the logger on first usage, instead of on LogManager instantiation.
        // Avoids dependency issues while testing.
        if(!logger) {
            logger = _logManager.getLogger('utils');
        }

        var args = Array.prototype.slice.call(arguments),
            func;
        func = args.shift();
        if (typeof(func) === 'function') {
            try {
                return func.apply(null, args);
            } catch (e) {
                logger.error('Exception occured: ' + e.message + '\n' + e.stack);
            }
        }
    };
}

export function makeSafeForCSS(name) {
    if(!name){
        return name;
    } else {
        return name.replace(/[^a-z0-9]/g, function(s) {
            var c = s.charCodeAt(0);
            if (c === 32) {
                return '-';
            }
            if (c >= 65 && c <= 90) {
                return '_' + s.toLowerCase();
            }
            return '__' + ('000' + c.toString(16)).slice(-4);
        });
    }
}
