import {WebRtcAdaptorModel} from './webRtcAdaptorModel';

export function WebRtcPluginAdaptorModel() {
    var self = {},
        //this variable will be always set by a plugin adaptor.
        pluginVersion = {
            major: 0,
            minor: 0,

            min_revision: 0,
            min_build: 0,

            current_revision: 0,
            current_build: 0
        };

    var base = WebRtcAdaptorModel();
    Object.assign(self, base);

    self.getPluginVersion = function() {
        return pluginVersion;
    };

    self.setPluginVersion = function(version) {
        pluginVersion = version;
    };

    self.getUserMediaContraints = function() {
        return {
            audio: true,
            video: true
        };
    };

    return self;
}
