import {webRtcLibraryDecoratorImpl} from './webRtcLibraryDecorator';
import {webRtcLibraryChromeDecoratorImpl} from './webRtcLibraryChromeDecorator';
import {webRtcLibraryFirefoxDecoratorImpl} from './webRtcLibraryFirefoxDecorator';

export default {
    WebRtcDecoratorFactory: container =>
        target => webRtcLibraryDecoratorImpl(target, container.Utils),
    WebRtcChromeDecoratorFactory: container =>
        target => webRtcLibraryChromeDecoratorImpl(target, container.Global, container.Navigator, container.Utils, container.LogManager),
    WebRtcFirefoxDecoratorFactory: container =>
        target => webRtcLibraryFirefoxDecoratorImpl(target, container.WebRtcChromeDecoratorFactory, container.Global, container.Navigator)
};
