import calls from './calls'
import devices from './devices'
import localVideo from './localVideo'
import media from './media'
import audioBridges from './audioBridges'

/**
 * Combine subreducers into a single call reducer.
 */
export default function (state = {}, action) {
  return {
    calls: calls(state.calls, action),
    devices: devices(state.devices, action),
    media: media(state.media, action),
    localVideo: localVideo(state.localVideo, action),
    audioBridges: audioBridges(state.audioBridges, action)
  }
}
