import { WEBRTC_DEVICE_KINDS } from '../constants'

/**
 * Converts an array of MediaDevices into an object keyed on device type.
 * @method parseDevices
 * @param  {Array} [devices=[]] Media devices, in the form of MediaDeviceInfo objects.
 * @return {Object} Media device object.
 */
export function convertDevices (devices = []) {
  const convertedDevices = {
    microphone: [],
    camera: [],
    speaker: []
  }

  devices.forEach(function (device) {
    convertedDevices[WEBRTC_DEVICE_KINDS[device.kind]].push(device)
  })

  return convertedDevices
}
