import * as actionTypes from '../actionTypes'
import { handleActions } from 'redux-actions'

const defaultState = {
  displaying: false
}

const reducers = {}

// Store information about the local video being displayed.
reducers[actionTypes.START_LOCAL_VIDEO_FINISH] = {
  next (state, action) {
    return {
      displaying: true,
      container: action.payload
    }
  }
}

// Remove info / Return to default state.
reducers[actionTypes.STOP_LOCAL_VIDEO_FINISH] = {
  next () {
    return defaultState
  }
}

const reducer = handleActions(reducers, defaultState)
export default reducer
