import {getProperty} from '../../utils';
import CONSTANTS from '../../constants';

export function CallControlServiceImpl({
    Http: _server,
    LogManager: _logManager,
    Cache: _cache,
    Config: _config,
    SdpPipeline: _sdpPipeline
}) {

    var logger = _logManager.getLogger('callControlService');

    function runSdpPipeline(callid, sdp, operation, type) {
        return _sdpPipeline(callid, sdp, operation, CONSTANTS.WEBRTC.SDP.STEP.PRE_SEND_LOCAL, type);
    }

    function addNotificationChannel(data) {
        if (_config.anonymous && _cache.getItem('NotificationId')) {
            data.callMeRequest.notifyChannelId = _cache.getItem('NotificationId');
        }
    }

    function errorParser(jqXHR) {
        if (jqXHR && jqXHR.responseText) {
            return JSON.parse(jqXHR.responseText).callControlResponse;
        }
    }

    this.startCall = function(from, to, sdp, onSuccess, onFailure, customParameters) {
        const modifiedSdp = runSdpPipeline(undefined, sdp, CONSTANTS.WEBRTC.SDP.OPERATION.START_CALL, CONSTANTS.WEBRTC.SDP.TYPE.OFFER);

        logger.info('Call Start Function: ' + from + ' --> ' + to);

        // response of the startCall contains callid/sessionData
        // callMe and callControl returns same response but object types have different namse
        function parseCallStart(data) {
            var callid, response = _config.anonymous ? data.callMeResponse : data.callControlResponse;
            if (response) {
                callid = response.sessionData;
            }
            return callid;
        }

        function dataType() {
            var data;
            if (_config.anonymous) {
                data = {
                    'callMeRequest': {
                        'type': 'callStart',
                        'from': from,
                        'to': to,
                        'sdp': modifiedSdp
                    }
                };
                if (customParameters) {
                    data.callMeRequest.customParameters = customParameters;
                }
            } else {
                data = {
                    'callControlRequest': {
                        'type': 'callStart',
                        'from': from,
                        'to': to,
                        'sdp': modifiedSdp
                    }
                };
                if (customParameters) {
                    data.callControlRequest.customParameters = customParameters;
                }
                if (_config.earlyMedia === true ||
                    _config.callReplace === true) {
                    data.callControlRequest.supported = [];
                }
                if (_config.earlyMedia === true) {
                    data.callControlRequest.supported.push('earlymedia');
                }
                if (_config.callReplace === true) {
                    data.callControlRequest.supported.push('replacecall');
                }
            }
            return data;
        }

        var data = dataType(),
            realm = _config.realm;
        addNotificationChannel(data);

        _server.sendPostRequest({
            'url': _server.getWAMUrl(1, _config.anonymous ? '/callMe' + (realm ? ('?tokenrealm=' + realm) : '') : '/callControl'),
            'data': data
        },
        onSuccess,
        onFailure,
        parseCallStart,
        errorParser
        );
    };

    this.audit = function(callid, onSuccess, onFailure) {
        var data, realm = _config.realm;

        if (_config.anonymous) {
            data = {
                'callMeRequest': {
                    'type': 'audit'
                }
            };
        } else {
            data = {
                'callControlRequest': {
                    'type': 'audit'
                }
            };
        }

        //TODO JF verify if we need to always do that and not only for callMe realm;
        if (realm) {
            callid = callid.split('%0A')[0];
        }

        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, (_config.anonymous ? '/callMe/callSessions/' : '/callControl/callSessions/') + callid + (realm ? ('?tokenrealm=' + realm) : '')),
            'data': data
        }, onSuccess, onFailure, null, errorParser);
    };

    this.hold = function(callid, sdp, onSuccess, onFailure, customParameters) {
        const modifiedSdp = runSdpPipeline(callid, sdp, CONSTANTS.WEBRTC.SDP.OPERATION.HOLD, CONSTANTS.WEBRTC.SDP.TYPE.OFFER);

        logger.info('Hold Function : sdp : ' + modifiedSdp);
        var data, realm = _config.realm;

        if (_config.anonymous) {
            data = {
                'callMeRequest':
                {
                    'type':'startCallUpdate',
                    'sdp': modifiedSdp
                }
            };
            if (customParameters) {
                data.callMeRequest.customParameters = customParameters;
            }
        }
        else {
            data = {
                'callControlRequest':
                {
                    'type':'startCallUpdate',
                    'sdp': modifiedSdp
                }
            };
            if (customParameters) {
                data.callControlRequest.customParameters = customParameters;
            }
        }
        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, (_config.anonymous ? '/callMe/callSessions/' : '/callControl/callSessions/') + callid + (realm?('?tokenrealm=' + realm):'')),
            'data': data
        }, onSuccess, onFailure, null, errorParser);
    };

    this.unhold = function(callid, sdp, onSuccess, onFailure, customParameters) {
        const modifiedSdp = runSdpPipeline(callid, sdp, CONSTANTS.WEBRTC.SDP.OPERATION.UNHOLD, CONSTANTS.WEBRTC.SDP.TYPE.OFFER);

        logger.info('UnHold Function : sdp : ' + modifiedSdp);
        var data, realm = _config.realm;

        if (_config.anonymous) {
            data = {
                'callMeRequest':
                {
                    'type':'startCallUpdate',
                    'sdp': modifiedSdp
                }
            };
            if (customParameters) {
                data.callMeRequest.customParameters = customParameters;
            }
        }
        else {
            data = {
                'callControlRequest':
                {
                    'type':'startCallUpdate',
                    'sdp': modifiedSdp
                }
            };
            if (customParameters) {
                data.callControlRequest.customParameters = customParameters;
            }
        }

        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, (_config.anonymous ? '/callMe/callSessions/' : '/callControl/callSessions/') + callid + (realm?('?tokenrealm=' + realm):'')),
            'data': data
        }, onSuccess, onFailure, null, errorParser);
    };

    this.reinvite = function(callid, sdp, onSuccess, onFailure, customParameters) {
        const modifiedSdp = runSdpPipeline(callid, sdp, CONSTANTS.WEBRTC.SDP.OPERATION.UPDATE, CONSTANTS.WEBRTC.SDP.TYPE.OFFER);

        logger.info('reinvite Function : sdp : ' + modifiedSdp);

        var data, realm = _config.realm;

        if (_config.anonymous) {
            data = {
                'callMeRequest':
                {
                    'type':'startCallUpdate',
                    'sdp': modifiedSdp
                }
            };
            if (customParameters) {
                data.callMeRequest.customParameters = customParameters;
            }
        }
        else {
            data = {
                'callControlRequest':
                {
                    'type':'startCallUpdate',
                    'sdp': modifiedSdp
                }
            };
            if (customParameters) {
                data.callControlRequest.customParameters = customParameters;
            }
        }

        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, (_config.anonymous ? '/callMe/callSessions/' : '/callControl/callSessions/') + callid + (realm?('?tokenrealm=' + realm):'')),
            'data': data
        }, onSuccess, onFailure, null, errorParser);
    };

    this.respondCallUpdate = function(callid, sdp, onSuccess, onFailure, customParameters, operation=CONSTANTS.WEBRTC.SDP.OPERATION.UPDATE) {
        const modifiedSdp = runSdpPipeline(callid, sdp, operation, CONSTANTS.WEBRTC.SDP.TYPE.ANSWER);

        logger.info('Respond Call Update Function : sdp : ' + modifiedSdp);
        var data, realm = _config.realm;

        if (_config.anonymous) {
            data = {
                'callMeRequest':
                {
                    'type':'respondCallUpdate',
                    'sdp': modifiedSdp
                }
            };
            if (customParameters) {
                data.callMeRequest.customParameters = customParameters;
            }
        }
        else {
            data = {
                'callControlRequest':
                {
                    'type':'respondCallUpdate',
                    'sdp': modifiedSdp
                }
            };
            if (customParameters) {
                data.callControlRequest.customParameters = customParameters;
            }
        }

        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, (_config.anonymous ? '/callMe/callSessions/' : '/callControl/callSessions/') + callid + (realm?('?tokenrealm=' + realm):'')),
            'data': data
        }, onSuccess, onFailure, null, errorParser);
    };

    this.respondToRemoteHold = function(callid, sdp, onSuccess, onFailure, customParameters) {
        this.respondCallUpdate(callid, sdp, onSuccess, onFailure, customParameters, CONSTANTS.WEBRTC.SDP.OPERATION.HOLD);
    };

    this.respondToRemoteUnhold = function(callid, sdp, onSuccess, onFailure, customParameters) {
        this.respondCallUpdate(callid, sdp, onSuccess, onFailure, customParameters, CONSTANTS.WEBRTC.SDP.OPERATION.UNHOLD);
    };

    this.join = function(firstSessionData, secondSessionData, sdp, onSuccess, onFailure, customParameters) {
        const modifiedSdp = runSdpPipeline(undefined, sdp, CONSTANTS.WEBRTC.SDP.OPERATION.UPDATE, CONSTANTS.WEBRTC.SDP.TYPE.OFFER);

        logger.info('Join Function : sdp : ' + modifiedSdp);

        function parseJoin(data) {
            var callid, response = data.callControlResponse;

            if (response) {
                callid = response.sessionData;
            }

            return callid;
        }

        var data = {
            'callControlRequest': {
                'type': 'join',
                'firstSessionData': firstSessionData,
                'secondSessionData': secondSessionData,
                'sdp': modifiedSdp
            }
        };
        if (customParameters) {
            data.callControlRequest.customParameters = customParameters;
        }

        if (_config.clientControlled === 'true') {
            data.callControlRequest.clientControlled = 'true';
        }


        _server.sendPostRequest({
            'url': _server.getWAMUrl(1, '/callControl/'),
            'data': data
        },
        onSuccess,
        onFailure,
        parseJoin,
        errorParser
        );
    };

    this.refer = function(callid, referTo, referredBy, onSuccess, onFailure) {
        logger.info('Refer Function : refer to: ' + referTo);
        var data = {
            'callControlRequest': {
                'type': 'refer',
                'from': referredBy,
                'to': referTo
            }
        };

        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, '/callControl/callSessions/' + callid),
            'data': data
        }, onSuccess, onFailure, null, errorParser);
    };

    this.endCall = function(callid, onSuccess, onFailure, localReasonText) {
        var realm = _config.realm;
        logger.info('endCall Function: ' + callid);

        var endUrl = (_config.anonymous ? '/callMe/callSessions/' : '/callControl/callSessions/') + callid;
        var queryString = (localReasonText ? ('?reasonText=' + localReasonText) : '');
        if(realm) {
            let delimiter = queryString ? '&' : '?';
            queryString += (realm ? (delimiter + 'tokenrealm=' + realm) : '');
        }

        _server.sendDeleteRequest({
            'url': _server.getWAMUrl(1, endUrl + queryString),
            'data': {}
        },
        onSuccess,
        onFailure,
        null,
        errorParser
        );
    };

    this.answerCall = function(callid, sdp, onSuccess, onFailure, customParameters) {
        const modifiedSdp = runSdpPipeline(callid, sdp, CONSTANTS.WEBRTC.SDP.OPERATION.ANSWER_CALL, CONSTANTS.WEBRTC.SDP.TYPE.ANSWER);

        logger.info('Answer Call Function : sdp : ' + modifiedSdp);

        var data = {
            'callControlRequest': {
                'type': 'callAnswer',
                'sdp': modifiedSdp
            }
        };
        if (customParameters) {
            data.callControlRequest.customParameters = customParameters;
        }

        if (_config.callReplace === true) {
            data.callControlRequest.supported = ['replacecall'];
        }

        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, '/callControl/callSessions/' + callid),
            'data': data
        }, onSuccess, onFailure, null, errorParser);
    };

    function makeRequest(action, sessionData, onSuccess, onFailure, address) {
        logger.info('makeRequest Function with action : ' + action);
        var data = {
            'callDispositionRequest': {
                'action': action,
                'sessionData': sessionData
            }
        };
        if (address) {
            data.callDispositionRequest.address = address;
        }
        _server.sendPostRequest({
            'url': _server.getWAMUrl(1, '/calldisposition'),
            'data': data
        },
        onSuccess,
        onFailure,
        null,
        errorParser
        );
    }

    this.sendCustomParameters = function(callid, customParameters, onSuccess, onFailure) {
        logger.info('sendCustomParameters Function with parameters : ' + customParameters);
        var data, realm = _config.realm;
        if (_config.anonymous) {
            data = {
                'callMeRequest': {
                    'type': 'sendCustomHeaders',
                    'customParameters': customParameters
                }
            };
        } else {
            data = {
                'callControlRequest': {
                    'type': 'sendCustomHeaders',
                    'customParameters': customParameters
                }
            };
        }
        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, (_config.anonymous ? '/callMe/callSessions/' : '/callControl/callSessions/') + callid + (realm?('?tokenrealm=' + realm):'')),
            'data': data
        }, onSuccess, onFailure, null, errorParser);
    };


    this.reject = function(callid, onSuccess, onFailure) {
        var dummy;
        logger.info('Reject Function: ' + callid);
        makeRequest('reject', callid, onSuccess, onFailure, dummy);
    };


    this.forward = function(callid, address, onSuccess, onFailure) {
        logger.info('Forward Function : address: ' + address);
        makeRequest('forward', callid, onSuccess, onFailure, address);
    };

    this.transfer = function(callid, address, sessiondataToTransfer, onSuccess, onFailure) {
        logger.info('Call Transfer Function : target address: ' + address);
        var data = {
            'callControlRequest': {
                'type': 'transfer',
                'address': address,
                'sessionData': sessiondataToTransfer
            }
        };
        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, '/callControl/callSessions/' + callid),
            'data': data
        }, onSuccess, onFailure, null, errorParser);
    };

    this.clickToCall = function(callingParty, calledParty, onSuccess, onFailure) {
        var data = {
            'clickToCallRequest': {
                'callingParty': callingParty,
                'calledParty': calledParty
            }
        };
        _server.sendPostRequest({
            'url': _server.getWAMUrl(1, '/clicktocall'),
            'data': data
        },
        onSuccess,
        onFailure
        );
    };

    this.getIMRN = function(realm, source, destination, onSuccess, onFailure) {
        logger.info('(Wam Call) getIMRN Function ');

        function parseIMRNResponse(IMRNdata) {
            var receivedIMRN;
            if (IMRNdata && IMRNdata.imrnResponse) {
                receivedIMRN = getProperty(IMRNdata.imrnResponse, 'imrn');
            }
            return receivedIMRN;
        }

        if (destination.match('@')) {
            if (destination.split(':')[0] !== 'sip') {
                destination = 'sip:' + destination;
            }
        }

        var data = {
            'imrnRequest': {
                'realm': realm,
                'sourceAddress': source,
                'destinationAddress': destination
            }
        };
        _server.sendPostRequest({
            'url': _server.getWAMUrl(1, '/imrn'),
            'data': data
        },
        onSuccess,
        onFailure,
        parseIMRNResponse
        );
    };

    this.acceptCallReplace = function(callData, onSuccess, onFailure, customParameters) {
        function parseAcceptCallReplaceData(data) {
            logger.info('acceptCallReplace.data: ' + data);
            return data;
        }

        function dataType() {
            var data;
            data = {
                'callControlRequest': {
                    'type': 'acceptCallReplace',
                    'sdp': callData.sdp
                }
            };
            if (customParameters) {
                data.callControlRequest.customParameters = customParameters;
            }

            return data;
        }
        var data = dataType();

        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, '/callControl/callSessions/' + callData.id),
            'data': data
        },
        onSuccess,
        onFailure,
        parseAcceptCallReplaceData,
        errorParser
        );
    };

    this.rejectCallReplace = function(callData, onSuccess, onFailure) {
        function parseRejectCallReplaceData(data) {
            logger.info('rejectCallReplace.data: ' + data);
            return data;
        }

        function dataType() {
            var data;
            data = {
                'callControlRequest': {
                    'type': 'rejectCallReplace',
                }
            };
            return data;
        }
        var data = dataType();
        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, '/callControl/callSessions/' + callData.id),
            'data': data
        },
        onSuccess,
        onFailure,
        parseRejectCallReplaceData,
        errorParser
        );
    };

    this.ringing = function(callid) {
        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, '/callControl/callSessions/' + callid),
            'data': {
                'callControlRequest': {
                    'type': 'ringing'
                }
            }
        }, null, null, null, errorParser);
    };

    this.updateIceCandidate = function(callData, onSuccess, onFailure) {
        var realm = _config.realm;

        _server.sendPutRequest({
            'url': _server.getWAMUrl(1, (_config.anonymous ? '/callMe/callSessions/' : '/callControl/callSessions/') + callData.id + (realm ? ('?tokenrealm=' + realm) : '')),
            'data': {
                'callControlRequest': {
                    'type': 'updateIceCandidate',
                    'iceCandidates': callData.iceCandidates
                }
            }
        }, onSuccess, onFailure, null, errorParser);
    };

    this.getSession = function(callid, onSuccess, onFailure) {
        _server.sendGetRequest({
            'url': _server.getWAMUrl(1, (_config.anonymous ? '/callMe/callSessions/' : '/callControl/callSessions/') + callid)
        }, onSuccess, onFailure, null, errorParser);
    };

}
