import {WebRtcAdaptorModel} from './webRtcAdaptorModel';

export function WebRtcChromeAdaptorModel() {
    var self = {},
        mediaSourceId;

    var base = WebRtcAdaptorModel();
    Object.assign(self, base);

    self.getMediaSourceId = function() {
        return mediaSourceId;
    };

    self.setMediaSourceId = function(sourceId) {
        mediaSourceId = sourceId;
    };

    return self;
}
