import {WebRtcAdaptorImpl} from './webRtcAdaptor';
import {WebRtcChromeAdaptorImpl} from './webRtcChromeAdaptor';
import {WebRtcContainerAdaptorImpl} from './webRtcContainerAdaptor';
import {WebRtcFirefoxAdaptorImpl} from './webRtcFirefoxAdaptor';
import {WebRtcFirefoxEsrAdaptorImpl} from './webRtcFirefoxEsrAdaptor';
import {WebRtcPluginAdaptorImpl} from './webRtcPluginAdaptor';
import {WebRtcPluginv22AdaptorImpl} from './webRtcPluginv22Adaptor';
import {WebRtcPluginv31AdaptorImpl} from './webRtcPluginv31Adaptor';

export default {
    WebRtcAdaptorBaseFactory: container =>
        (decorator, model) =>
            new WebRtcAdaptorImpl({decorator, model, ...container }),

    WebRtcChromeAdaptorFactory: container =>
        function(base, decorator, model) {

            decorator = decorator || container.WebRtcChromeDecoratorFactory;
            model = model || container.WebRtcChromeAdaptorModelFactory();
            base = base || container.WebRtcAdaptorBaseFactory(decorator, model);

            return new WebRtcChromeAdaptorImpl({ base, decorator, model, ...container });
        },

    WebRtcContainerAdaptorFactory: container =>
        function(base, decorator, model) {

            decorator = decorator || container.WebRtcChromeDecoratorFactory;
            model = model || container.WebRtcChromeAdaptorModelFactory();
            base = base || container.WebRtcAdaptorBaseFactory(decorator, model);

            return new WebRtcContainerAdaptorImpl({ base, decorator, model, ...container });
        },

    WebRtcFirefoxAdaptorFactory: container =>
        function(base, decorator, model) {

            decorator = decorator || container.WebRtcFirefoxDecoratorFactory;
            model = model || container.WebRtcFirefoxAdaptorModelFactory();
            base = base || container.WebRtcAdaptorBaseFactory(decorator, model);

            return new WebRtcFirefoxAdaptorImpl({ base, decorator, model, ...container });
        },

    WebRtcFirefoxEsrAdaptorFactory: container =>
        function(base, decorator, model) {

            decorator = decorator || container.WebRtcFirefoxDecoratorFactory;
            model = model || container.WebRtcFirefoxAdaptorModelFactory();
            base = base || container.WebRtcFirefoxAdaptorFactory(undefined, decorator, model);

            return new WebRtcFirefoxEsrAdaptorImpl({ base, decorator, model, ...container });
        },

    WebRtcPluginAdaptorFactory: container =>
        function(base, decorator, model) {

            decorator = decorator || container.WebRtcDecoratorFactory;
            model = model || container.WebRtcPluginAdaptorModelFactory();
            base = base || container.WebRtcAdaptorBaseFactory(decorator, model);

            return new WebRtcPluginAdaptorImpl({ base, decorator, model, ...container });
        },

    WebRtcPluginv22AdaptorFactory: container =>
        function(base, decorator, model) {

            decorator = decorator || container.WebRtcDecoratorFactory;
            model = model || container.WebRtcPluginAdaptorModelFactory();
            base = base || container.WebRtcPluginAdaptorFactory(decorator, model);

            return new WebRtcPluginv22AdaptorImpl({ base, decorator, model, ...container });
        },

    WebRtcPluginv31AdaptorFactory: container =>
        function(base, decorator, model) {

            decorator = decorator || container.WebRtcDecoratorFactory;
            model = model || container.WebRtcPluginAdaptorModelFactory();
            base = base || container.WebRtcPluginAdaptorFactory(base, decorator, model);

            return new WebRtcPluginv31AdaptorImpl({ base, decorator, model, ...container });
        },
};
