import * as actionTypes from '../actionTypes'

/**
 * Represents that media should be initialized.
 * @method initMedia
 * @param {Object} [options]
 * @param {string} [options.chromeExtensionId] ID of the Chrome Screenshare extension to be used.
 * @returns {Object} A flux standard action.
 */
export function initMedia (options = {}) {
  return {
    type: actionTypes.INIT_MEDIA,
    payload: options
  }
}

/**
 * Represents that media has been / tried to be initialized.
 * @method initMediaFinish
 * @param  {Object} result Information about media support.
 * @param  {boolean} result.error Whether the initialization was successful or not.
 * @param  {number} result.code A unique code describing the result scenario.
 * @param  {string} result.message Human readable message of the result.
 * @returns {Object} A flux standard action.
 */
export function initMediaFinish (result) {
  return {
    type: actionTypes.INIT_MEDIA_FINISH,
    payload: result
  }
}

/**
 * Represents an action to prompt user for permission to use speakers or camera.
 * @method promptUserMedia
 * @param {Object} [params]
 * @param {boolean} [params.video]
 * @param {boolean} [params.audio]
 * @returns {Object} A flux standard action.
 */
export function promptUserMedia (options) {
  return {
    type: actionTypes.PROMPT_USER_MEDIA,
    payload: options
  }
}

/**
 * Represents the finish of an action to prompt user for permission to use speakers or camera.
 * @method promptUserMediaFinish
 * @param  {Object} [error] The error object, in the case of an error.
 * @param {Object} [params]
 * @param {boolean} [params.video]
 * @param {boolean} [params.audio]
 * @returns {Object} A flux standard action.
 */
export function promptUserMediaFinish (options, error) {
  if (typeof error !== 'undefined') {
    return {
      type: actionTypes.PROMPT_USER_MEDIA_FINISH,
      payload: { options: options, error: error },
      error: !!error
    }
  } else {
    return {
      type: actionTypes.PROMPT_USER_MEDIA_FINISH,
      payload: options
    }
  }
}
