import {LogManagerImpl} from './logmanager/logManager';
import {Utils} from './utils';
import {Config} from './config';
import {Cache} from './cache';
import {SdpParserImpl} from './sdpparser';
import {ChallengeManagerFactory} from './challenge';
import {CoreImpl} from './core';
import {LocaleService} from './locale';

import httpModule from './http/module';
import globalBroadcasterModule from './globalbroadcaster/module';
import turnCredentialsModule from './turncredentials/module';
import sdpModule from './sdp/module';

export default {
    ... httpModule,
    ... globalBroadcasterModule,
    ... turnCredentialsModule,
    ... sdpModule,

    Config: () => Config(),
    LogManager: container => new LogManagerImpl(container),
    Utils: container => new Utils(container),
    Cache: container => new Cache(container),
    SdpParser: container => new SdpParserImpl(container),
    LocaleService: container => new LocaleService(container),
    ChallengeManager: ChallengeManagerFactory,
    Core: container => new CoreImpl(container),
    NotificationCallbacks: () => { return {}; },

    // Some globals
    Navigator: () => navigator,
    Global: () => window
};
