import transform from 'sdp-transform';

/*
 * This library is wrapped by functions in this module so that we can change
 * the way that SDP is parsed later if we need to.
 */

export function sdpParse(sdpString) {
    return transform.parse(sdpString);
}

export function sdpWrite(sdpObject) {
    return transform.write(sdpObject);
}
