import { createConvertedSdpPipeline } from './pipeline';
import createSdpLogger from './logger';
import createBundleHandler from './bundleHandler';

export default {
    SdpPipeline: container => {
        const builtinHandlers = [
            createBundleHandler(() => container.Config)
        ];

        return createConvertedSdpPipeline(
            () => [...container.Config.sdpHandlers, ...builtinHandlers],
            createSdpLogger(container.LogManager.getLogger('sdpLogger'))
        );
    }
};
