// Import a call shim between FCS calls and Next version of SDK.
// It is used to handle FCS' call object, since we cannot put it in sate.
import callShimFactory from './callShim'

import { fork, put } from 'redux-saga/effects'
import * as authActionTypes from '../../auth/interface/actionTypes'
import * as notiActionTypes from '../../notifications/interface/actionTypes'
import * as connActionTypes from '../../connectivity/interface/actionTypes'
import {
  getPlatform,
  getRequestInfo,
  getAuthConfig,
  getUserInfo,
  getConnectionInfo
} from '../../auth/interface/selectors'
import { update as updateConfig } from '../../config/interface/actions'

import { checkMediaDevices, watchCheckMediaDevices, onDeviceChange } from './sagas'
import { mapEvents } from '../../events/interface/actions'
import BasicError, { callCodes } from '../../errors'

// Utilities.
import { defaults, isFunction, omit, pick } from 'lodash/fp'
import { logManager as logMgr } from '../../logs'
import { getCpaasAgentHeaderValue } from '../../request/utils'

// Import the interface to implement.
import pluginInterface from '../interface'
import { callsActions, devicesActions, localVideoActions, mediaActions, audioBridgeActions } from '../interface/actions'
import * as actionTypes from '../interface/actionTypes'
import { getCallById, getCallConfig, getMediaInfo } from '../interface/selectors'
import events from '../interface/events'

// Constants
import { platforms } from '../../constants'

// get logger
const log = logMgr.getLogger('CALL')
const fcsLog = logMgr.getLogger('FCS')

// eslint-disable-next-line no-warning-comments
/**
 * Configuration options for the call feature.
 * @public
 * @name config.call
 * @memberof config
 * @instance
 * @param {Object} call The call configuration object.
 * @param {Object} [call.callDefaults] Default options to be used when making/answering a call.
 * @param {boolean} [call.callDefaults.isAudioEnabled=true] Specifies whether audio is enabled or not.
 * @param {boolean} [call.callDefaults.isVideoEnabled=true] Specifies whether video is enabled or not.
 * @param {boolean} [call.callDefaults.sendInitialVideo=false] Specifies whether to send an initial video stream or not.
 * @param {Object} [call.callDefaults.remoteVideoContainer] Specifies the container where video (coming from remote party) is rendered.
 * @param {Object} [call.callDefaults.localVideoContainer] Specifies the container where video (coming from local party) is rendered.
 * @param {string} [call.chromeExtensionId] ID of the screenshare extension being used for screenshare of Google Chrome.
 * @param {boolean} [call.recordCallStats=false] Whether to enable the recording of call statistics as part of app's local storage.
 * @param {boolean} [call.earlyMedia=false] Whether to use early media (e.g. for playing incoming tones) as part of an outgoing call.
 * @param {number} [call.callAuditTimer=30000] Audit time value for calls, as a positive number in milliseconds.
 * @param {number} [call.activeCallTimeoutMS=120000] Timeout for an existing ringing call before it gets terminated, as a positive number in milliseconds.
 * @param {boolean} [call.ringingFeedback] When enabled, inform Spidr that RingingFeedback is supported.
 * @param {string} [call.codecsToReplace] Specifies alternative audio/video codecs to use for a given call. It has been deprecated so pipeline parameter should be used instead.
 * @param {boolean} [call.videoInactiveOnHold=false] Sets the video as "inactive" instead of "sendonly" when holding a call.
 * @param {boolean} [call.forceDisableMediaOnHold=false] Disables any type of media (e.g. Comfort Noise) from transmitting when call is held locally.
 * @param {number} [call.iceCandidateCollectionTimeoutInterval=3000] When provided (in milliseconds), ice candidate collection is assumed to be completed if at least one candidate is received within the interval.
 * @param {boolean} [call.relayCandidateCollectionTimeoutCycle=false] When enabled, iceCandidateCollectionTimeoutInterval is restarted until receiving first relay candidate. If the provided cycle limit is reached, ice candidate collection assumed to be completed.
 * @param {boolean} [call.recordCallStats=false] When enabled, call statistics are recorded in app's localstorage after the call is terminated.
 * @param {Object} [call.callConstraints] Custom RTCPeerConnection constraints to use for calls. Will cause errors if malformed.
 * @param {Object} [call.callConstraints.chrome] Custom constraints to be used on Google Chrome.
 * @param {Object} [call.callConstraints.firefox] Custom constraints to be used on Mozilla Firefox.
 * @param {string} [call.bundlePolicy='DISABLED'] The bundle policy to use for peer connections. Value can be fcs.SDP_CONSTANTS.BUNDLE_POLICY.MAX_COMPAT, fcs.SDP_CONSTANTS.BUNDLE_POLICY.MAX_BUNDLE, fcs.SDP_CONSTANTS.BUNDLE_POLICY.BALANCED or fcs.SDP_CONSTANTS.BUNDLE_POLICY.DISABLED. The DISABLED option means that bundle group lines will be removed from every SDP.
 * @param {Object} [call.opusConfig] Bandwidth controls to add for Opus audio codec.
 * @param {number} [call.opusConfig.maxPlaybackRate] Maximum playback rate, in bits per second. Must be a positive value between 8000 and 48000.
 * @param {number} [call.opusConfig.maxAverageBitrate] A bitrate encoding value between 6000 and 510000 bits per second.
 * @param {number} [call.opusConfig.fec] Specifies whether Forward Error Correction is enabled or not. When enabled, FEC provides robustness against packet loss. Acceptable values can only be 0 or 1.
 * @param {number} [call.opusConfig.dtx] Specifies whether Discontinuous Transmission mode is enabled or not. When enabled, DTX reduces the bitrate during silence or background noise. Acceptable values can only be 0 or 1.
 * @param {number} [call.opusConfig.ptime] Packet (i.e. frame) duration in milliseconds. Frames will be combined into packets to achieve the maximum of 120 ms duration. A positive value between 2.5 and 120.
 * @param {number} [call.webrtcLogCollectionInterval=3000] Interval at which to collect WebRTC logs for calls, in milliseconds.
 * @param {boolean} [call.useRelay=false] Whether we should force connection through the relay candidates (i.e. TURN server). Mostly used for testing.
 * @param {string} [call.trickleIceSupport='none'] Whether we should advertise and use Trickle ICE. Accepted value is one of: 'none', 'half' or 'full'.
 * @param {boolean} [call.continuity=false] Whether an existing voice call can be persisted, as a mobile phone moves between circuit switched and packet switched domains (e.g. GSM to WiFi).
 * @param {boolean} [call.resyncOnConnect=false] Whether all active calls should be resynched upon connecting or reconnecting to the websocket (requires Kandy Link 4.7.1+).
 */

/**
 * CallsLink factory. For call-related configs, see the makeCall API documentation.
 * @method callsLink
 * @param {Object} [options={}] Plugin configs. See above.
 * @return {Object} Call plugin.
 */
export default function callsLink (options = {}) {
  const defaultOptions = {
    // Values used as defaults for making/answering a call.
    callDefaults: {
      isAudioEnabled: true,
      isVideoEnabled: true,
      sendInitialVideo: false,
      remoteVideoContainer: undefined,
      localVideoContainer: undefined
    },
    // Values passed to FCS.
    webrtcdtls: true,
    chromeExtensionId: undefined,
    serverProvidedTurnCredentials: false,
    iceserver: [],
    recordCallStats: false
  }

  const callDefaults = defaults(defaultOptions.callDefaults, options.callDefaults)
  options = defaults(defaultOptions, options)
  options.callDefaults = callDefaults

  function * init () {
    // Send the provided options to the store.
    // This will be `state.config[name]`.
    yield put(updateConfig(options, pluginInterface.name))
    yield put(mapEvents(events))

    // Check media devices on setup. Do this as a non-blocking effect,
    //      since there is an async portion to it.
    yield fork(checkMediaDevices)
  }

  return {
    middleware,
    api: pluginInterface.api,
    capabilities: ['call'],
    name: pluginInterface.name,
    reducer: pluginInterface.reducer,
    init,
    sagas: [watchCheckMediaDevices, onDeviceChange]
  }
}

function middleware ({ dispatch, getState }) {
  var config = {}
  const shimContext = {
    // Provide the values as a function, so that we
    // don't call getState before the store is created.
    getUsername: function () {
      return getUserInfo(getState()).username
    }
  }
  const callShim = callShimFactory(shimContext)

  // FCS logger.
  function fcsLogger (loggerName, level, logObject) {
    const msg = logObject.timestamp + ' - ' + loggerName + ' - ' + level + ' - ' + logObject.message

    // Protect against args being undefined.
    const extras = logObject.args || []

    fcsLog.debug(msg, ...extras)
  }

  /*
   * Register a listener on FCS to dispatch an action when we receive a call.
   * @method onReceived
   * @param  {Object} call An FCS call object.
   */
  callShim.onIncomingCall = function (callId, callInfo) {
    dispatch(callsActions.callIncoming(callId, callInfo))
  }

  callShim.onCallStateChange = function (callId, state, transition, callInfo = {}) {
    // Add information about the transition that we can determine using
    //      previous state and new state.
    const prevCallState = getCallById(getState(), callId)
    transition.prevState = prevCallState.state
    transition.newState = state

    // TODO: Once this if/else chain grows, move this as a utility.
    if (prevCallState.state === 'RINGING' && state === 'IN_CALL') {
      // Call is first established.
      callInfo.startTime = Date.now()
      transition.event = 'callEstablished'
    } else if (prevCallState.state === state && prevCallState.remoteVideoState !== callInfo.remoteVideoState) {
      // If only the remoteVideoState property changed...
      transition.event = 'remoteVideoChange'
    } else if (state === 'ENDED') {
      const now = Date.now()
      callInfo.endTime = now
      // If there isn't a start time, then the call was never completed.
      //      Meaning it's duration was 0, so set the start time appropriately.
      if (!prevCallState.startTime) {
        callInfo.startTime = now
      }
    }

    // Dispatch an action on state change.
    dispatch(callsActions.callStateChange(callId, state, transition, callInfo))
  }

  callShim.onMediaStateChange = function (callId, mediaState) {
    // Dispatch an action on media state change.
    dispatch(callsActions.callMediaStateChange(callId, mediaState))
  }

  callShim.onBridgeCallsChanged = function (bridgeId, calls) {
    // Dispatch an action on bridge state change
    dispatch(audioBridgeActions.updateAudioBridgeCalls(bridgeId, calls))
  }

  return next => action => {
    switch (action.type) {
      case actionTypes.SET_DEFAULT_DEVICES: {
        log.debug('Setting default call devices.', action.payload)
        const devices = pick(['camera', 'microphone', 'speaker'], action.payload)
        callShim
          .setDefaultDevices(devices)
          .then(function (setDevices) {
            dispatch(devicesActions.setDefaultDevicesFinish({ devices: setDevices }))
          })
          .catch(function (error) {
            dispatch(devicesActions.setDefaultDevicesFinish({ error }))
          })
        break
      }

      case actionTypes.PROMPT_USER_MEDIA: {
        log.debug('Getting user media.')
        callShim
          .promptUserMedia(action.payload)
          .then(function ({ params }) {
            dispatch(mediaActions.promptUserMediaFinish(params))
          })
          .catch(function ({ params, basicError }) {
            dispatch(mediaActions.promptUserMediaFinish(params, basicError))
          })
        break
      }

      case actionTypes.CHANGE_SPEAKER: {
        log.debug('Change current speaker used for call audio.', action.payload)
        callShim
          .changeSpeaker(action.payload)
          .then(function (speakerId) {
            dispatch(devicesActions.changeSpeakerFinish({ speakerId }))
          })
          .catch(function ({ speakerId, error }) {
            dispatch(devicesActions.changeSpeakerFinish({ speakerId, error }))
          })
        break
      }

      case actionTypes.CHANGE_INPUT_DEVICES: {
        log.debug('Changing current camera/microphone used for call.', action.payload.callId)
        callShim
          .changeInputDevices(action.payload.callId)
          .then(function (callId) {
            dispatch(devicesActions.changeInputDevicesFinish({ callId }))
          })
          .catch(function ({ callId, error }) {
            dispatch(devicesActions.changeInputDevicesFinish({ callId, error }))
          })
        break
      }

      case authActionTypes.CONNECT: {
        // Set up FCS logging if enabled.
        // Do it here since it's the earliest not-at-store-creation point
        //      where we use FCS.
        // TODO: Selector for logs state?
        if (getState().config.logs.enableFcsLogs) {
          // Set the fcsLog handler to directly log to console. The fcsLogger
          //    should have already formatted the messages. An application won't
          //    have a chance to handle these logs directly, but that's v3.X ...
          fcsLog.logHandler = entry => {
            console.debug(...entry.messages)
          }
          // Set the fcsLog level to debug.
          fcsLog.level = 'DEBUG'

          // Provide the fcsLogger (which calls the above fcsLog) to FCS.
          callShim.initLogging(fcsLogger, true)
        }

        const { username, password, authname } = action.payload.credentials
        // Do this here since it's the last point we have access to the password.
        callShim.setUserAuth(username, password, authname)

        // Retrieve configs for use within the middleware scope.
        // This has to be done after init*, hence after connect, but only
        //      needed once, so not at the base of the middleware scope.
        config = getCallConfig(getState())

        // Set connection info in FCS
        const authConfig = getAuthConfig(getState())
        var connectionInfo = {
          protocol: authConfig.subscription.protocol,
          restUrl: authConfig.subscription.server,
          restPort: authConfig.subscription.port,
          websocketProtocol: authConfig.websocket.protocol,
          websocketIP: authConfig.websocket.server,
          websocketPort: authConfig.websocket.port,
          // Need to set callResyncOnConnect into FCS config here
          callResyncOnConnect: config.resyncOnConnect,
          // Hook into FCS Call requests
          ajaxHook: function (xhr, unusedWindow, { url, headers }) {
            const platform = getPlatform(getState())

            // Get and set the X-Cpaas-Agent header value to be sent with all call
            // control requests.  NOTE: X-Cpaas-Agent is already set for non call
            // related requests in the request plugin.
            const cpaasAgentHeaderValue = getCpaasAgentHeaderValue(platform, url)
            headers['X-Cpaas-Agent'] = cpaasAgentHeaderValue

            // Request headers/urls are populated differently depending on whether it is Link or UC
            if (platform === platforms.LINK) {
              const requestInfo = getRequestInfo(getState(), platform)
              const customHeaders = requestInfo.requestOptions.headers
              // Include custom x-session header if it exists.
              // (Used by subsequent requests after authenticated & subscribed via HMAC token)
              if (customHeaders['x-session']) {
                headers['x-session'] = customHeaders['x-session']
              }
              return {
                url: url,
                headers: headers
              }
            } else if (platform === platforms.UC) {
              const { oauthToken, accessToken } = getConnectionInfo(getState(), platforms.UC)
              if (oauthToken) {
                // For OAuth handling, add the Bearer token to the Request Header
                headers = {
                  ...headers,
                  Authorization: `Bearer ${oauthToken}`
                }
              } else {
                // For regular Access Token handling, add the token to the URL string
                const tokenString = `token=${accessToken}`
                if (url.indexOf('?') === -1) {
                  url += '?' + tokenString
                } else {
                  url += '&' + tokenString
                }
              }
              return {
                url: url,
                headers: headers
              }
            }
          }
        }
        log.debug('Setting connection info configs in FCS', connectionInfo)
        callShim.setConnectionInfo(connectionInfo)
        break
      }

      case authActionTypes.CONNECT_FINISHED: {
        if (action.error) {
          // Don't setup FCS if the login failed.
          break
        }

        const hasCallService =
          action.payload.subscription.service.indexOf('call') > -1 ||
          action.payload.subscription.service.indexOf('callMe') > -1

        // Log a warning if the user isn't subscribed for calls.
        if (!hasCallService) {
          // TODO: An event should be emitted from this. So the app knows to check services /
          //      update services to try and get calls.
          log.error('Error: Setting up for calls without a call service subscription. Calls will likely fail.')
        }

        if (action.meta.isSSO) {
          // If we are in an SSO scenario we first have to call fcs' setTokenAuth endpoint.
          // For normal CONNECT scenario's the equivalent is setUserAuth in the CONNECT middleware above.
          // For SSO we do it here since we don't get the username until after the connect server response.
          callShim.setTokenAuth(action.payload.userInfo.username, action.payload.userInfo.token)

          // This call tells FCS to use UAT's instead of basic auth for all future rest calls.
          callShim.setKandyUAT(action.payload.userInfo.token)
        }

        // Pull out the FCS' setup configs from the plugin configs.
        const fcsConfig = omit(['callDefaults', 'chromeExtensionId'], config)

        // Update the ice server info provided to FCS with the turn credentials
        //      retrieved from subscription.
        if (fcsConfig.serverProvidedTurnCredentials && fcsConfig.hasOwnProperty('iceserver')) {
          const turnCredentials = action.payload.subscription.turnCredentials || {}
          if (turnCredentials.username && turnCredentials.password) {
            log.debug('Using server provided turn credentials.')
            fcsConfig.iceserver = fcsConfig.iceserver.map(function (iceInfo) {
              return {
                ...iceInfo,
                username: turnCredentials.username,
                credential: turnCredentials.password
              }
            })
          } else {
            // TODO: Should this dispatch an error action? Or is a warning
            //      enough, since an error will be encountered when calling?
            log.warn('Warning: Server turn credentials not found.')
          }
        }

        if (config.chromeExtensionId) {
          fcsConfig.screenSharing = {
            chromeExtensionId: config.chromeExtensionId
          }
        }

        // If there are plugin configs not present in setupVars,
        // add them in now.
        var setupVars = fcsConfig
        if (action.payload.connection.isAnonymous) {
          setupVars.anonymous = true
        }
        if (action.payload.connection.realm) {
          setupVars.realm = action.payload.connection.realm
          callShim.setRealm(action.payload.connection.realm)
        }

        // Don't log passwords.
        const toLog = defaults(setupVars, {
          iceserver: setupVars.iceserver.map(function (server) {
            return {
              ...server,
              credential: '********'
            }
          })
        })
        log.debug('Setting up FCS with configs (connection info, including hooks, already set): ', toLog)

        // Setup the callstack.
        callShim.setup(setupVars)

        // Initialize media.
        dispatch(mediaActions.initMedia(setupVars))
        break
      }
      case actionTypes.MAKE_CALL: {
        const callId = action.payload.callId
        const options = defaults(config.callDefaults, action.payload.callInfo)
        if (!options.from) {
          // If no `from` is provided, use the current user.
          options.from = getUserInfo(getState()).username
        }
        log.debug('Making call with options: ', options)

        // Call the shim's makeCall function.
        callShim
          .makeCall(callId, options)
          // FCS' startCall success callback.
          .then(function ({ options }) {
            dispatch(callsActions.makeCallFinish(callId, options))
          })
          // Failure scenario for either startCall or initMedia.
          .catch(function ({ error, options }) {
            dispatch(callsActions.makeCallFinish(callId, options, error))
          })

        break
      }

      case actionTypes.ANSWER_CALL: {
        const options = defaults(config.callDefaults, action.payload.callInfo)
        options.to = getUserInfo(getState()).username
        log.debug('Answering call with options: ', options)

        callShim
          .answerCall(action.payload.callId, options)
          .then(function ({ callId }) {
            dispatch(callsActions.answerCallFinish(callId, options))
          })
          .catch(function ({ error, callId }) {
            dispatch(callsActions.answerCallFinish(callId, options, error))
          })
        break
      }

      case actionTypes.IGNORE_CALL: {
        log.debug('Ignoring call.', action.payload.callId)
        callShim
          .ignoreCall(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.ignoreCallFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.ignoreCallFinish(callId, error))
          })
        break
      }

      case actionTypes.REJECT_CALL: {
        log.debug('Rejecting call.', action.payload.callId)
        callShim
          .rejectCall(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.rejectCallFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.rejectCallFinish(callId, error))
          })
        break
      }

      case actionTypes.END_CALL: {
        log.debug('Ending call.', action.payload.callId)
        callShim
          .endCall(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.endCallFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.endCallFinish(callId, error))
          })
        break
      }

      case actionTypes.MUTE_CALL: {
        log.debug('Muting call.', action.payload.callId)
        callShim
          .muteCall(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.muteCallFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.muteCallFinish(callId, error))
          })
        break
      }

      case actionTypes.UNMUTE_CALL: {
        log.debug('Unmuting call.', action.payload.callId)
        callShim
          .unMuteCall(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.unMuteCallFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.unMuteCallFinish(callId, error))
          })
        break
      }

      case actionTypes.SILENCE_CALL: {
        log.debug('Silencing call.', action.payload.callId)
        callShim
          .silenceCall(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.silenceCallFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.silenceCallFinish(callId, error))
          })
        break
      }

      case actionTypes.UNSILENCE_CALL: {
        log.debug('Unsilencing call.', action.payload.callId)
        callShim
          .unSilenceCall(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.unSilenceCallFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.unSilenceCallFinish(callId, error))
          })
        break
      }

      case actionTypes.SET_CUSTOM_PARAMETERS: {
        log.debug('Setting custom parameters.')
        callShim.setCustomParameters(action.payload.callId, action.payload.customParameters)
        break
      }

      case actionTypes.START_VIDEO: {
        log.debug('Starting call video.', action.payload.callId)
        callShim
          .startVideo(action.payload.callId, action.payload.callInfo)
          .then(function ({ callId, params }) {
            dispatch(callsActions.startVideoFinish(callId, params))
          })
          .catch(function ({ callId, params, error }) {
            dispatch(callsActions.startVideoFinish(callId, params, error))
          })
        break
      }

      case actionTypes.STOP_VIDEO: {
        log.debug('Stopping call video.', action.payload.callId)
        callShim
          .stopVideo(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.stopVideoFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.stopVideoFinish(callId, error))
          })
        break
      }

      case actionTypes.HOLD_CALL: {
        log.debug('Hold call.', action.payload.callId)
        callShim
          .holdCall(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.holdCallFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.holdCallFinish(callId, error))
          })
        break
      }

      case actionTypes.UNHOLD_CALL: {
        log.debug('Unhold call.', action.payload.callId)
        callShim
          .unHoldCall(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.unHoldCallFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.unHoldCallFinish(callId, error))
          })
        break
      }

      case actionTypes.SEND_DTMF: {
        log.debug('Sending DTMF tone to call.', action.payload.callId)
        callShim
          .sendDTMF(action.payload.callId, action.payload.params.tone)
          .then(function () {
            log.info('DTMF tone sent.')
            // Nothing to be stored in state for DTMF tone.
          })
          .catch(function ({ callId, error }) {
            log.info('DTMF tone could not be sent.')
            dispatch(callsActions.sendDTMFFinish(callId, error))
          })
        break
      }

      case actionTypes.SEND_CUSTOM_PARAMETERS: {
        log.debug('Sending custom parameters.', action.payload.callId)
        callShim
          .sendCustomParameters(action.payload.callId)
          .then(function () {
            log.info('Custom parameters sent.')
            // Nothing to be stored in state.
          })
          .catch(function ({ callId, error }) {
            log.info('Custom parameters could not be sent.')
            dispatch(callsActions.sendCustomParametersFinish(callId, error))
          })
        break
      }

      case actionTypes.START_SCREENSHARE: {
        log.debug('Starting screenshare on call.', action.payload.callId)

        /**
         * Special case callback function.
         * Triggers when screensharing is stopped by the browser.
         * Defined in this scope so that it knows about the redux level.
         */
        const onScreenshareStop = function () {
          log.debug('Screenshare stopped manually.')
          dispatch(callsActions.stopScreenshareFinish(action.payload.callId))
        }

        callShim
          .startScreenshare(action.payload.callId, action.payload.callInfo.screenshareOptions, onScreenshareStop)
          .then(function ({ callId, params }) {
            dispatch(callsActions.startScreenshareFinish(callId, params))
          })
          .catch(function ({ callId, params, error }) {
            dispatch(callsActions.startScreenshareFinish(callId, params, error))
          })
        break
      }

      case actionTypes.STOP_SCREENSHARE: {
        log.debug('Stopping screenshare on call.', action.payload.callId)
        callShim
          .stopScreenshare(action.payload.callId)
          .then(function ({ callId }) {
            dispatch(callsActions.stopScreenshareFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.stopScreenshareFinish(callId, error))
          })
        break
      }

      case actionTypes.FORWARD_CALL: {
        log.debug(`Forwarding call to user ${action.payload.params.destination}`, action.payload.callId)
        callShim
          .forwardCall(action.payload.callId, action.payload.params.destination)
          .then(function ({ callId }) {
            dispatch(callsActions.forwardCallFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.forwardCallFinish(callId, error))
          })
        break
      }

      case actionTypes.DIRECT_TRANSFER: {
        log.debug(`Transferring call (direct) to user ${action.payload.params.destination}`, action.payload.callId)
        callShim
          .directTransfer(action.payload.callId, action.payload.params.destination)
          .then(function ({ callId }) {
            dispatch(callsActions.directTransferFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.directTransferFinish(callId, error))
          })
        break
      }

      case actionTypes.CONSULTATIVE_TRANSFER: {
        log.debug(
          `Transferring call (consultative) to destination ${action.payload.params.destination}`,
          action.payload.callId
        )
        callShim
          .consultativeTransfer(action.payload.callId, action.payload.params.destination)
          .then(function ({ callId }) {
            dispatch(callsActions.consultativeTransferFinish(callId))
          })
          .catch(function ({ callId, error }) {
            dispatch(callsActions.consultativeTransferFinish(callId, error))
          })
        break
      }

      case actionTypes.JOIN_CALL: {
        log.debug(`Joining call ${action.payload.params.destination}`, action.payload.callId)
        callShim
          .joinCall(action.payload.callId, action.payload.params.destination)
          .then(function ({ callId: joinedCallId, joinedCalls, callState }) {
            const state = getState()
            const username = getUserInfo(state).username
            const joinedCallTo = joinedCalls.map(function (callId) {
              const call = getCallById(state, callId)
              return call.to === username ? call.from : call.to
            })
            // Add as much info as we can to the joined call.
            const callInfo = {
              state: callState,
              from: username,
              to: 'Joined Call',
              participants: joinedCallTo,
              // Mark this call as "special".
              isJoinedCall: true,
              joinedCalls,
              // TODO: These values are assumed...
              isAudioEnabled: true,
              isVideoEnabled: false,
              muted: false
            }

            dispatch(
              callsActions.joinCallFinish(joinedCallId, joinedCalls, {
                callInfo
              })
            )
          })
          .catch(function ({ callId: joinedCallId, joinedCalls, error }) {
            dispatch(callsActions.joinCallFinish(joinedCallId, joinedCalls, { error }))
          })
        break
      }

      case actionTypes.START_LOCAL_VIDEO: {
        log.debug('Starting local video.')
        const videoContainer = action.payload || config.localVideoContainer
        callShim
          .startLocalVideo(videoContainer)
          .then(function (container) {
            dispatch(localVideoActions.startLocalVideoFinish(container))
          })
          .catch(function ({ error }) {
            let basicError
            // Convert the callShim error object into a BasicError.
            if (error.type === 'media') {
              basicError = new BasicError({
                message: error.message,
                code: callCodes.INIT_MEDIA_FAILED
              })
            } else if (error.type === 'video') {
              basicError = new BasicError({
                message: error.message,
                code: callCodes.USER_MEDIA_ERROR
              })
            } else if (error.type === 'generic') {
              basicError = new BasicError({
                message: error.message,
                code: callCodes.GENERIC_ERROR
              })
            } else {
              basicError = new BasicError({
                message: 'An error occurred when starting local video.',
                code: callCodes.UNKNOWN_ERROR
              })
            }

            dispatch(localVideoActions.startLocalVideoFinish(null, basicError))
          })
        break
      }

      case actionTypes.STOP_LOCAL_VIDEO: {
        log.debug('Stopping local video.')
        callShim
          .stopLocalVideo()
          .then(function () {
            dispatch(localVideoActions.stopLocalVideoFinish())
          })
          .catch(function (error) {
            const basicError = new BasicError({
              message: error.message,
              code: callCodes.GENERIC_ERROR
            })
            dispatch(localVideoActions.stopLocalVideoFinish(basicError))
          })
        break
      }

      case actionTypes.INIT_MEDIA: {
        // Move around the extension ID to be where FCS will look for it.
        //      Done the same way as before setup is done.
        if (action.payload.chromeExtensionId) {
          action.payload.screenSharing = {
            chromeExtensionId: action.payload.chromeExtensionId
          }
        }

        const callState = defaults(getCallConfig(getState()), action.payload)
        const { isConnected } = getState().authentication

        const mediaResults = getMediaInfo(getState())
        // Only allow initMedia before connecting. Doing initMedia will
        //      override any settings used the last time initMedia was called,
        //      so only allow manual initMedia before connection, since
        //      connection is where iceserver settings are retrieved from server.
        // TODO: Fix "this" properly (ie. fix FCS' initMedia).
        // Side-note: FCS' results from second+ attempt may not be accurate.
        if (!isConnected) {
          log.debug('Initializing media.')
          callShim.initMedia(callState).then(function (result) {
            dispatch(mediaActions.initMediaFinish(result))
          })
        } else {
          log.debug('Media already initialized; re-using results.')
          dispatch(mediaActions.initMediaFinish(mediaResults))
        }
        break
      }

      case actionTypes.CREATE_AUDIO_BRIDGE: {
        log.debug('Creating audio bridge.', action.payload.bridgeId)
        callShim
          .createLocalAudioBridge(action.payload.bridgeId)
          .then(function () {
            dispatch(audioBridgeActions.createAudioBridgeFinish(action.payload.bridgeId))
          })
          .catch(function (error) {
            dispatch(audioBridgeActions.createAudioBridgeFinish(action.payload.bridgeId, error))
          })
        break
      }

      case actionTypes.CLOSE_AUDIO_BRIDGE: {
        log.debug('Closing audio bridge.', action.payload.bridgeId)
        callShim
          .closeAudioBridge(action.payload.bridgeId)
          .then(function () {
            dispatch(audioBridgeActions.closeAudioBridgeFinish(action.payload.bridgeId))
          })
          .catch(function (error) {
            dispatch(audioBridgeActions.closeAudioBridgeFinish(action.payload.bridgeId, error))
          })
        break
      }

      /*
       * BRIDGE_ADD_CALL action is fired without .then(), as the bridge's active list of calls is kept up to date
       * in the state through the UPDATE_AUDIO_BRIDGE_CALLS action, which is emitted by the bridge itself.
       * UPDATE_AUDIO_BRIDGE_CALLS is mapped to the BRIDGE_CHANGE event.
       *
       * `addCallToBridge.catch()` is still used for error handling.
       */
      case actionTypes.BRIDGE_ADD_CALL: {
        const { bridgeId, callId } = action.payload
        log.debug('Adding call to bridge.', bridgeId)
        callShim.addCallToBridge(bridgeId, callId).catch(function (error) {
          dispatch(audioBridgeActions.addCallToBridgeFinish(bridgeId, callId, error))
        })
        break
      }

      /*
       * BRIDGE_REMOVE_CALL action is fired without .then(), as the bridge's active list of calls is kept up to date
       * in the state through the UPDATE_AUDIO_BRIDGE_CALLS action, which is emitted by the bridge itself.
       * UPDATE_AUDIO_BRIDGE_CALLS is mapped to the BRIDGE_CHANGE event.
       *
       * `removeCallFromBridge.catch()` is still used for error handling.
       */
      case actionTypes.BRIDGE_REMOVE_CALL: {
        const { bridgeId, callId } = action.payload
        log.debug('Removing call to bridge.', bridgeId)
        callShim.removeCallFromBridge(bridgeId, callId).catch(function (error) {
          dispatch(audioBridgeActions.removeCallFromBridgeFinish(bridgeId, callId, error))
        })
        break
      }

      case actionTypes.MUTE_AUDIO_BRIDGE: {
        log.debug('Muting audio bridge.', action.payload.bridgeId)
        callShim
          .muteAudioBridge(action.payload.bridgeId)
          .then(function () {
            dispatch(audioBridgeActions.muteAudioBridgeFinish(action.payload.bridgeId))
          })
          .catch(function (error) {
            dispatch(audioBridgeActions.muteAudioBridgeFinish(action.payload.bridgeId, error))
          })
        break
      }

      case actionTypes.UNMUTE_AUDIO_BRIDGE: {
        log.debug('Unmuting audio bridge.', action.payload.bridgeId)
        callShim
          .unmuteAudioBridge(action.payload.bridgeId)
          .then(function () {
            dispatch(audioBridgeActions.unmuteAudioBridgeFinish(action.payload.bridgeId))
          })
          .catch(function (error) {
            dispatch(audioBridgeActions.unmuteAudioBridgeFinish(action.payload.bridgeId, error))
          })
        break
      }

      case actionTypes.SILENCE_AUDIO_BRIDGE: {
        log.debug('Silencing audio bridge.', action.payload.bridgeId)
        callShim
          .silenceAudioBridge(action.payload.bridgeId)
          .then(function () {
            dispatch(audioBridgeActions.silenceAudioBridgeFinish(action.payload.bridgeId))
          })
          .catch(function (error) {
            dispatch(audioBridgeActions.silenceAudioBridgeFinish(action.payload.bridgeId, error))
          })
        break
      }

      case actionTypes.UNSILENCE_AUDIO_BRIDGE: {
        log.debug('Unsilencing audio bridge.', action.payload.bridgeId)
        callShim
          .unsilenceAudioBridge(action.payload.bridgeId)
          .then(function () {
            dispatch(audioBridgeActions.unsilenceAudioBridgeFinish(action.payload.bridgeId))
          })
          .catch(function (error) {
            dispatch(audioBridgeActions.unsilenceAudioBridgeFinish(action.payload.bridgeId, error))
          })
        break
      }

      case notiActionTypes.NOTIFICATION_RECEIVED: {
        // TODO: Limit this to only handle call related messages.
        //      Or generalize this to another plugin/handler.
        const data = action.payload.notificationMessage
        if (data) {
          const callbacks = callShim.getNotificationCallbacks()
          if (isFunction(callbacks[data.eventType])) {
            const channel = action.meta.channel
            log.debug(`${channel} notification message received: ${data.eventType}`)
            // Handle websocket messages by forwarding them to FCS' notification system.
            callbacks[data.eventType](data)
          }
        }
        break
      }

      case connActionTypes.LOST_CONNECTION: {
        const globalBroadcaster = callShim.getGlobalBroadcaster()
        globalBroadcaster.publish('NOTIFICATION_CHANNEL_LOST', {})
        break
      }

      case connActionTypes.WS_CONNECT_FINISHED: {
        const globalBroadcaster = callShim.getGlobalBroadcaster()
        globalBroadcaster.publish('DEVICE_SUBSCRIPTION_STARTED', {})
        break
      }

      default:
        break
    }

    return next(action)
  }
}
